<template>
  <div>
    <div class="machineTable">
      <img
        src="../../../assets/images/loading.gif"
        class="loadgif"
        id="show_loading2"
      />

      <p></p>
    </div>
    <el-row type="flex" justify="space-between">
      <el-col style="width: fit-content" v-if="!showSelect">
        <el-button
          type="primary"
          @click="openAddRowDialog"
          icon="el-icon-plus"
          class="plusicon"
          style="margin-right: 0.1rem"
          ></el-button
        >
      </el-col>
      <el-col>
        <el-popconfirm
          confirm-button-text=""
          cancel-button-text=""
          icon="el-icon-info"
          title=""
          cancel-button-type="primary"
          @confirm="showSelectFunc('')"
          @cancel="showSelectFunc('')"
          :disabled="loading"
        >
          <el-button
            type="primary"
            class="plusicon"
            v-if="!showSelect"
            slot="reference"
            :disabled="loading"
          >
            <span></span>
          </el-button>
        </el-popconfirm>
        <div v-if="showSelect" style="width: fit-content">
          <el-button
            type="text"
            @click="confirmSelectFunc"
            class="plusicon"
            v-if="showSelect"
            style="margin-right: 0.1rem"
          >
            <span></span>
          </el-button>
          <el-button
            type="text"
            @click="showSelectFunc('')"
            class="plusicon"
            v-if="showSelect"
            style="margin-right: 0.1rem; color: grey"
          >
            <span></span>
          </el-button>
          <span class="report_msg"
            >
            <span style="font-weight: bold"> {{ messageinfo }} </span
            ></span
          >
        </div>
      </el-col>
      <el-button
        v-if="!showsearch && !showSelect"
        type="primary"
        @click="showsearch = true"
        icon="el-icon-search"
        class="plusicon"
        title=""
      ></el-button>
      <el-col
        :span="20"
        style="display: flex; justify-content: end"
        v-else-if="!showSelect"
      >
        <filtercom
          :filterinfo="[
            '',
            '',
            '',
            '',
            '',
          ]"
          ref="filtercom"
          @searchFunc="searchFunc"
        >
        </filtercom>
        <!-- <el-button
          type="primary"
          @click="searchFunc"
          icon="el-icon-search"
          class="plusicon"
          ></el-button
        > -->
      </el-col>
    </el-row>
    <div class="table-container" :style="{ height: heightValue }">
      <el-table
        bordered
        row-class-name="rowstyle"
        :data="paginatedData"
        style="width: 100%; background: transparent"
        :cell-class-name="getCellClassName"
        height="100%"
        v-loading="loading"
        :key="showSelect"
        @selection-change="handleSelectionChange"
        ref="multipleTable"
      >
        <el-table-column label="" v-if="!showSelect" minWidth="90rem">
          <template #default="scope">
            <el-button
              v-if="operate[1] == 'delete'"
              @click="confirmDeleteRow(scope.row)"
              type="text"
              size="small"
              style="color: red; font-size: 0.175rem"
            >
              
            </el-button>
            <el-button
              @click="editRow(scope.row)"
              type="text"
              size="small"
              style="font-size: 0.175rem"
            >
              
            </el-button>
          </template>
        </el-table-column>
        <el-table-column type="selection" width="55" v-if="showSelect">
        </el-table-column>
        <el-table-column label="">
          <template #default="scope">
            {{ scope.row["addFlag"] == 1 ? "" : "" }}
          </template>
        </el-table-column>
        <el-table-column
          fit
          v-for="(column, index) in columns"
          :key="index"
          :prop="column.prop"
          :label="column.label"
          :sortable="column.sortable"
          v-if="column.label != 'ID' && column.label != 'ID'"
        >
          <template #default="scope">
            <div v-if="!scope.row.editing">
              <div
                v-if="column.prop == ''"
                @click="showbigimg(scope.row[column.prop])"
              >
                <img
                  :src="scope.row[column.prop]"
                  style="width: 0.5rem"
                  v-if="scope.row[column.prop]"
                />
                <span v-else></span>
              </div>
              <span
                v-else-if="
                  column.prop != '' && column.prop != ''
                "
              >
                <span v-if="column.prop.indexOf('') >= 0">{{
                  scope.row[column.prop].replace("T00:00:00", "")
                }}</span>
                <span v-else>
                  <span v-if="column.prop == ''">
                    {{
                      formatShowData(scope.row[column.prop], "gpdShutoffType")
                    }}
                  </span>
                  <span v-if="column.prop == ''">
                    {{
                      formatShowData(scope.row[column.prop], "gpdGoggleType")
                    }}
                  </span>
                  <span v-if="column.prop == ''">
                    {{
                      formatShowData(
                        scope.row[column.prop],
                        "gpdGoggleDrivemode"
                      )
                    }}
                  </span>
                  <span
                    v-else-if="
                      column.prop != '' &&
                      column.prop != '' &&
                      column.prop != ''
                    "
                  >
                    {{ scope.row[column.prop] }}
                  </span>
                </span>
              </span>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <el-button
        type="primary"
        @click="
          $downloadExcel({
            tableData: tabledata,
            title: '',
          })
        "
        class="plusicon"
        size="small"
        ></el-button
      >
    </div>

    <el-dialog
      :title="operatetitle"
      :visible.sync="addRowDialogVisible"
      :modal="false"
      top="0.8rem"
      v-if="operate[2] == 'add'"
      :close-on-click-modal="false"
    >
      <div class="dialog-content">
        <el-form
          :model="newRow"
          label-position="top"
          ref="newRow"
          :rules="rules_data"
        >
          <el-form-item
            v-for="column in columns"
            :key="column.prop"
            :label="column.label"
            :prop="column.prop"
            v-if="
              column.label != 'ID' &&
              column.label != 'ID' &&
              column.prop != '' &&
              column.prop != ''
            "
          >
            <div v-if="column.prop == ''">
              <el-radio-group v-model="newRow[column.prop]" disabled>
                <el-radio
                  v-for="city in checkdata1"
                  :label="city"
                  :key="city"
                  >{{ city }}</el-radio
                >
              </el-radio-group>
            </div>
            <el-select
              v-if="column.prop == ''"
              v-model="newRow[column.prop]"
              style="width: 8rem"
            >
              <el-option
                v-for="item in gpdShutoffType"
                :label="item.label"
                :value="item.value"
                :key="item.label"
              ></el-option>
            </el-select>
            <el-select
              v-if="column.prop == ''"
              v-model="newRow[column.prop]"
              style="width: 8rem"
            >
              <el-option
                v-for="item in gpdGoggleType"
                :label="item.label"
                :value="item.value"
                :key="item.label"
              ></el-option>
            </el-select>
            <el-select
              v-if="column.prop == ''"
              v-model="newRow[column.prop]"
              style="width: 8rem"
            >
              <el-option
                v-for="item in gpdGoggleDrivemode"
                :label="item.label"
                :value="item.value"
                :key="item.label"
              ></el-option>
            </el-select>
            <!-- <div v-if="column.prop == ''">
              <Uploadocom
                v-if="newRow[column.prop] == ''"
                :uploadAction="'/api/function/upload'"
                :showloading2="showloading2"
                @upload="listenUpload"
                @imageshow="(data) => imageshowFunc(data, column.prop)"
                ref="uploadocom"
              ></Uploadocom>
              <div style="width: 1rem" v-else>
                <img
                  :src="newRow[column.prop]"
                  style="width: 1rem; height: 1rem"
                />
                <div style="width: 1rem">
                  <span
                    style="color: #247cc3; cursor: pointer"
                    @click="cancelImg(column.prop)"
                    ></span
                  >
                </div>
              </div>
            </div> -->
            <el-date-picker
              v-if="column.prop.indexOf('') >= 0"
              v-model="newRow[column.prop]"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
            >
            </el-date-picker>
            <el-input
              v-else-if="
                column.prop != '' &&
                column.prop != '' &&
                column.prop != '' &&
                column.prop != '' &&
                column.prop != ''
              "
              v-model="newRow[column.prop]"
              :placeholder="'' + column.label"
              style="width: 8rem"
            ></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button
          @click="addRow('newRow')"
          type="primary"
          :loading="loadingbtn"
          ></el-button
        >
        <el-button @click="resetform"></el-button>
      </span>
    </el-dialog>

    <el-dialog
      :modal="false"
      title=""
      :visible.sync="confirmDeleteDialogVisible"
      @close="cancelDelete"
      width="5rem"
    >
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelDelete"></el-button>
        <el-button type="danger" @click="deleteRow(confirmedRow)"
          ></el-button
        >
      </span>
    </el-dialog>

    <el-dialog
      :modal="false"
      title=""
      :visible.sync="imgdialog"
      @close="imgdialog = false"
      custom-class="imgdialog"
      top="1rem"
    >
      <div style="width: 100%">
        <img :src="currimgsrc" style="width: 100%" />
      </div>
    </el-dialog>

    <el-pagination
      v-if="tableData.length > pageSize"
      class="el-pagin"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="total"
      layout="total, prev, pager, next, sizes"
      :page-sizes="pageSizes"
      ref="pangination_n"
    />
  </div>
</template>

<script>
import Filtercom from "@/views/components/filtercom.vue";
import Uploadocom from "../../components/upload.vue";
export default {
  props: [
    "column",
    "tabledata",
    "operate",
    "gpdShutoffType",
    "gpdGoggleType",
    "gpdGoggleDrivemode",
  ],

  components: { Uploadocom, Filtercom },
  data() {
    return {
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      pageSizes: [],
      total: 0,
      columns: [],
      addRowDialogVisible: false,
      newRow: {},
      confirmDeleteDialogVisible: false,
      confirmedRow: null,
      value: "",
      loadingbtn: false,
      deviceKind: ["", "", "", ""],
      deviceKind2: ["", "", ""],
      deviceKind3: ["", "", ""],
      showloading2: "show_loading2",
      imgdialog: false,
      currimgsrc: false,
      checkdata1: ["", "", ""],
      checkedData1: "",
      operatetitle: "",
      rules_data: {},
      showsearch: false,
      change_base: false,
      loading: false,
      showSelect: false,
      showSelectvalue: [],
      pre_currentPage: 1,
      heightValue: "calc(100vh - 3.2rem)",
      messageinfo: "",
    };
  },
  methods: {
    formatShowData(row, name) {
      //   console.log(row, name);
      //   console.log(this.gtType);
      if (this[name].find((info) => info.value == row)) {
        return this[name].find((info) => info.value == row).label;
      }
    },
      showSelectFunc(msg) {
        this.currentPage=1
        this.messageinfo = msg;
        this.$emit("chooseReport", msg);
        this.showSelect = !this.showSelect;
         if(msg==''){
          this.showSelectvalue=[]
         }
        this.$nextTick(() => {
          this.$forceUpdate(); //  Vue 
        });
      },
    confirmSelectFunc(msg) {
      this.$functionConfrim(this.showSelectvalue).then((result) => {
        console.log(result);
        if (result.status == "yes") {
          this.$emit("reportInfo", {
            checkevalue: this.showSelectvalue,
            operate: this.messageinfo,
          });
          //   this.$refs.multipleTable.clearSelection();
        }
      });
    },
    handleSelectionChange(val) {
      console.log(val);
      this.showSelectvalue = val;
    },
    searchFunc() {
      let params = Object.assign({}, this.$refs.filtercom.filterparams);
      console.log(params);

      this.$emit("searchparams", params);
    },
    setPageSizes() {
      const totalItems = this.tableData.length;
      this.pageSizes = [];

      //  [10, 20, 30, ...] 
      for (let i = 10; i <= totalItems; i += 10) {
        this.pageSizes.push(i);
      }

      // 10
      if (this.pageSizes.length === 0) {
        this.pageSizes.push(10);
      }
    },
    resetform() {
      this.addRowDialogVisible = false;
      this.operatetitle = "";
      this.initialform();
    },
    initialform() {
      this.$refs.newRow.resetFields();
    },
    cancelImg(column) {
      this.newRow[column] = "";
    },
    imageshowFunc(msg, column) {
      this.newRow[column] = msg;
    },
    showbigimg(src) {
      this.imgdialog = true;
      this.currimgsrc = src;
    },
    // update
    listenUpload() {
      // this.getSAPTable();
    },

    handleCurrentChange(page) {
      // console.log(this.showSelectvalue);
      if (this.showSelectvalue.length) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning",
        }).then(() => {
          // this.pageSize=0
          // console.log(this.$refs.pangination_n.internalCurrentPage);
          this.$nextTick(() => {
            // this.pageSize=10
            this.currentPage = this.pre_currentPage;
            this.$refs.pangination_n.internalCurrentPage = this.currentPage;
            this.$forceUpdate();
          });
          // console.log(this.currentPage,this.pre_currentPage);
        });
      } else {
        this.currentPage = page;
        this.pre_currentPage = page;
      }
    },
    handleSizeChange(size) {
      this.pageSize = size;
    },
    viewDetails(row) {
      console.log(":", row);
    },
    // 
    editRow(row) {
      this.addRowDialogVisible = true;
      //   this.newRow = JSON.parse(JSON.stringify(row));
      this.$nextTick(() => {
        this.newRow = JSON.parse(JSON.stringify(row));
        this.rules_data = this.$formatFormRules([], this.newRow, {});
      });
      this.operatetitle = "";
    },
    saveRow(row) {
      row.editing = false;
      console.log(":", row);
    },
    confirmDeleteRow(row) {
      if (this.paginatedData.length == 1) {
        this.$message({
          type: "warning",
          message: "",
          //   duration: 0,
          showClose: true,
        });
      } else {
        this.confirmDeleteDialogVisible = true;
        this.confirmedRow = row; // 
      }
    },
    cancelDelete() {
      this.confirmDeleteDialogVisible = false;
      this.confirmedRow = null; // 
    },
    deleteRow(row) {
      // const index = this.tableData.indexOf(row);
      console.log(row);
      this.$emit("deleterow", { rowinfo: row, operate: "" });
      // if (index > -1) {
      //   this.tableData.splice(index, 1);
      //   this.total = this.tableData.length; // 
      // }
      // this.cancelDelete(); // 
    },
    openAddRowDialog() {
      this.addRowDialogVisible = true;
      this.operatetitle = "";
      this.$nextTick(() => {
        this.formatRow();
        this.newRow[""] = "";

        this.rules_data = this.$formatFormRules([], this.newRow, {});
      });
    },
    addRow(formname) {
      // console.log(this.newRow)
      // console.log(this.$refs[formname])

      this.$refs[formname].validate((valid) => {
        // console.log(valid)
        if (valid) {
          this.$emit("addinfo", {
            rowinfo: this.newRow,
            operate: this.operatetitle,
          });
        } else {
          this.$message({
            message: "",
            type: "error",
          });
          return false;
        }
      });
    },
    getCellClassName({ row, column, rowIndex, columnIndex }) {
      if (row.age === "" && columnIndex === 2) {
        return "orange-warning"; // 
      } else {
        return "";
      }
    },
    // 
    formatRow() {
      this.newRow = this.columns.reduce((obj, column) => {
        obj[column.prop] = "";
        return obj;
      }, {});
    },
  },
  computed: {
    paginatedData() {
      this.columns = this.column;
      this.tableData = this.tabledata.map((item) => ({
        ...item,
      }));
      this.total = this.tableData.length;
      this.setPageSizes();
      //   this.rules_data = this.$formatFormRules(["t"], this.newRow);

      const start = (this.currentPage - 1) * this.pageSize;
      return this.tableData.slice(start, start + this.pageSize);
    },
  },
  mounted() {},
};
</script>

<style lang="less"></style>
