const isObjectEmpty = (obj) => {
    // console.log('qwqw', obj);
    return Object.keys(obj).every(
        (key) => obj[key] == null
    );
}




//form
/** 
@param{numberdata}  ["", "t//"]
@param{newRow} 
@param{specialform}  {
        : {
                required: false,
            },
*/
const formatFormRules = (numberdata, newRow, specialform) => {
    console.log(numberdata, newRow, specialform);
    let rulesdata = {};

    for (let i in newRow) {
        // console.log(numberdata, i);
        switch (true) {
            case numberdata.indexOf(i) >= 0:
                rulesdata[i] = [
                    {
                        required: true,
                        message: "",
                        trigger: "change",
                    },
                    {
                        pattern: /^(\d+(\.\d*)?|\.\d+)$/,
                        message: "",
                        trigger: "change",
                    },
                ];
                break;
            case (specialform && typeof specialform[i] == 'object') || (specialform && i == '') || (specialform && i == '' || (specialform && i == '') || (specialform && i == '') || (specialform && i == '')):
                rulesdata[i] = specialform[i]
                break
            default:
                rulesdata[i] = [
                    {
                        required: true,
                        message: "",
                        trigger: "change",
                    },
                ];
                break;
        }
    }
    return rulesdata
}

const functionConfirm = (value) => {
    return new Promise((resolve, reject) => {
        MessageBox.confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning',
        }).then(() => {
            console.log(value);
            resolve({ status: 'yes', value }); // 
        }).catch(() => {
            resolve({ status: 'no' }); // 
        });
    });
};


const downloadExcel = (value) => {
    let table_data = value.tableData.map(row => {
        const { ID, ID, ...rest } = row; // ID
        return rest;
    });
    // console.log(table_data);
    // Step 1: 
    const worksheet = XLSX.utils.json_to_sheet(table_data);

    // Step 2: 
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, value.title + '');

    // Step 3:  Excel 
    XLSX.writeFile(workbook, value.title + '.xlsx');
}
async function getId() {
    let res = await reportapi.getReportId();
    console.log(res);
    if (res.data.msg == "") {
        return res.data.data;
    } else {
        this.$message.error("ID");
        return false;
    }
}
