<template>
    <div>
        <!-- <span>{{ curr_clickdata }}</span> -->
        <div>
            <el-button :type="default_info == '...' ? 'primary' : 'info'" @click="startTimer"
                icon="el-icon-video-play"></el-button>

            <el-button :type="default_info == '' ? 'primary' : 'info'" @click="filterdata"
                icon="el-icon-search"></el-button>
        </div>
        <div v-if="default_info == '...'">
            <el-button type="text" @click="stoptime" icon="el-icon-video-pause"></el-button>
        </div>
        <div style="margin:0.1rem 0rem 0.1rem" v-if="default_info">
            <span>{{ default_info }}
                <span v-if="default_info == '...'">{{ time_value }}s</span>
            </span>
        </div>
        <div v-if="!default_info" style="margin-top: 0.1rem;">
            <div>
                <el-date-picker v-model="value1" type="datetimerange" range-separator="" start-placeholder=""
                    size="small" end-placeholder="" @change="changetime">
                </el-date-picker>
            </div>
            <div style="margin: 0.1rem 0;">
                <el-input-number v-model="interval" class="chartinput" label=""
                    size="small"></el-input-number>
                (1010s)
            </div>
            <div>
                <el-input-number v-model="num" class="chartinput" label="" size="small"></el-input-number>
                (100100)
                <el-button type="primary" @click="searchdata" icon="el-icon-search" style="float: right;"></el-button>

            </div>

        </div>
        <div :id="chartId" class="echarts-container" v-loading="loading"></div>

    </div>
</template>

<script>
import * as echarts from 'echarts';
import { querySingleCurve, queryCurve } from "@/api/watch/devicecurve";
import moment from 'moment'

export default {
    name: 'LineChart',
    props: {
        chartId: {
            type: String,
            required: true
        },
        // chartData: {
        //     type: Object,
        //     required: true
        // },
        curr_clickdata: {
            type: Object,
            required: false
        },
    },
    data() {
        return {
            chartInstance: null,
            timer_chart: null,
            chartData: {
                xData: [],
                yData: [],
                seriesName: ''
            },
            // default_info: "CO",paramRemark channelName
            default_info: ` ${this.curr_clickdata.channelName ? 'CO' : this.curr_clickdata.paramRemark} `,
            value1: [moment().subtract(1, 'hours'), moment()],
            interval: 0,
            num: 3600,
            starttime: moment().subtract(1, 'hours'),
            endtime: moment(),
            loading: true,
            time_value: 0
        };
    },
    mounted() {
        // this.initChart();
        this.getOneLine('init');
    },
    beforeDestroy() {
        if (this.chartInstance) {
            this.chartInstance.dispose();
        }
    },
    methods: {
        stoptime() {
            this.default_info = ` ${this.curr_clickdata.paramRemark} `
            this.disableTimerchart()
        },
        changetime(value) {
            // console.log(value);
            this.value1 = [moment(value[0]), moment(value[1])]
        },
        searchdata() {
            console.log(this.value1);
            console.log(this.interval);
            console.log(this.num);
            this.getOneLine();

        },
        filterdata() {
            this.disableTimerchart()
            this.default_info = ""
            this.chartData.xData = []
            this.chartData.yData = []
            this.updateChart()
        },
        disableTimerchart() {
            // console.log("hhh", this.timer);
            if (this.timer_chart != null) {
                this.time_value = 0
                clearInterval(this.timer_chart);
                this.timer_chart = null;
            }
        },
        startTimer() {
            this.loading = true

            this.interval = 0
            this.num = 3600
            if (this.timer_chart) clearInterval(this.timer_chart); // 
            this.default_info = "..."
            this.timer_chart = setInterval(async () => {
                this.value1 = [moment().subtract(1, 'hours'), moment()]
                this.time_value = this.time_value + 2
                await this.getOneLine();
            }, 2000); // 5
        },
        stopTimer() {
            if (this.timer_chart) {
                clearInterval(this.timer_chart);
                this.timer_chart = null;
            }
        },
        async getOneLine(value) {
            this.starttime = this.value1[0].format('YYYY-MM-DD HH:mm:ss');
            this.endtime = this.value1[1].format('YYYY-MM-DD HH:mm:ss');
            var qp1 = { "paramId": this.curr_clickdata.paramId, startTime: this.starttime, endTime: this.endtime, interval: this.interval, limit: this.num };

            try {
                let resp1 = await querySingleCurve(qp1);
                if (value == 'init') {
                    this.initChart();
                }
                this.updateChartData(resp1.data);

                // push
            } catch (error) {
                console.error('Failed to fetch new line data:', error);
                this.loading = false
                this.$message.error("")
            }
        },
        updateChartData(newData) {
            // 
            this.chartData.xData = [];
            this.chartData.yData = [];

            // 
            newData.forEach(item => {
                this.chartData.xData.push(item.ts);
                this.chartData.yData.push(item.val);
            });
            this.updateChart();
            // 
        },

        initChart() {
            const chartDom = document.getElementById(this.chartId);
            this.chartInstance = echarts.init(chartDom);
            this.setOptions();
        },
        setOptions() {
            this.chartData = {
                title: {
                    // text: this.chartData.title 
                    text: ""
                },
                tooltip: {
                    trigger: 'axis',
                    formatter: function (params) {
                        return `:${params[0].name}</br>CO:<span style="color:#28a745"> ${params[0].value}</span>`;
                    }
                },
                grid: {
                    top: '1.5%',
                    left: '3%',
                    right: '4%',
                    bottom: '5%',
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    data: this.chartData.xData || [],
                    axisLine: {
                        lineStyle: {
                            color: '#ffffff' // X 
                        }
                    },
                    axisTick: {
                        lineStyle: {
                            color: '#ffffff' // X 
                        }
                    },
                    axisLabel: {
                        color: '#ffffff' // X 
                    }
                },
                yAxis: {
                    min: 'dataMin',
                    max: 'dataMax',
                    type: 'value',
                    axisLine: {
                        lineStyle: {
                            color: '#ffffff' // Y 
                        }
                    },
                    axisTick: {
                        lineStyle: {
                            color: '#ffffff' // Y 
                        }
                    },
                    axisLabel: {
                        color: '#ffffff' // Y 
                    },
                    splitLine: {
                        show: false //  Y 
                    }
                },
                series: [
                    {
                        name: this.chartData.seriesName || '',
                        type: 'line',
                        data: this.chartData.yData || [],
                        itemStyle: {
                            color: 'red' // 
                        },
                        lineStyle: {
                            color: '#28a745', // 
                            width: 1 // 
                        }
                    }
                ],
                dataZoom: [
                    {
                        type: 'inside', //  dataZoom
                        start: 0,       // 
                        end: 10000        // 
                    },
                    {
                        type: 'slider', //  dataZoom
                        show: true,     // 
                        start: 0,       // 
                        end: 100,       // 
                        bottom: 0,     //  10px
                        handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3v-1.2c0-5,4-9.1,8.8-9.4C19.6,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.2h6.6V19.6z',
                        handleSize: '80%', // 
                        height: 15,         // 
                        borderColor: '#ccc', // 
                        backgroundColor: '#e2e2e2', // 
                        fillerColor: '#28a745', // 
                        handleStyle: {
                            color: '#fff',
                            shadowBlur: 3,
                            shadowColor: 'rgba(0, 0, 0, 0.6)',
                            shadowOffsetX: 2,
                            shadowOffsetY: 2
                        }
                    }
                ]
            };
            this.chartInstance.setOption(this.chartData);
            this.loading = false
        },
        updateChart() {
            if (this.chartInstance) {
                this.setOptions();
            }
        }
    },
};
</script>

<style scoped>
.echarts-container {
    width: 100%;
    height: 400px;
}
</style>