import CryptoJS from 'crypto-js';

// Base64AES
const ivBase64 = "x8CN1UhWRmry4wXesFfuDA==client_codeLFAzIpqwzMBqGUtNjJZ+bpellt9q+2xs2HKw3PNjwZQQ2YAk5/bj0w+gC9qJOn2zFgLhpaj1zswH3B2HZbLDTWsT2/4N/MhzJwM4sxEjE0DJwYwKcPflbD01hntxYk0hIBF3P0UA6VuQcKLOVbwdJyXIrqwswGqZfSRIjiqv5nU%3D"; // IV16Base64
const secretKeyBase64 = "15tyRz7Gz7/aHanqHO/1VbH32TJpZMpAqj3mZjzjWk8="; // 1632Base64

export default {
  decryptWithAES(encryptedData) {
    try {
      // Base64IVWordArray
      const iv = CryptoJS.enc.Base64.parse(ivBase64);
      const key = CryptoJS.enc.Base64.parse(secretKeyBase64);

      // 
      if (!encryptedData || typeof encryptedData !== 'string') {
        throw new Error('Invalid encrypted data');
      }

      // 
      let decrypted;
      try {
        decrypted = CryptoJS.AES.decrypt(
          encryptedData,
          key,
          { iv: iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 }
        );
      } catch (error) {
        console.error('Error during decryption:', error.message);
        throw error;
      }

      // 
      let decryptedString = decrypted.toString(CryptoJS.enc.Utf8);

      // 
      if (!decryptedString || decryptedString.trim() === '') {
        throw new Error('Decryption resulted in empty or invalid string');
      }

      //  JSON 
      try {
        return JSON.parse(decryptedString);
      } catch (e) {
        console.warn('Decrypted data is not valid JSON:', decryptedString);
        return decryptedString; //  JSON
      }
    } catch (error) {
      console.error(':', error.message);
      throw error;
    }
  },

  exampleUsage() {
    const encryptedData = "your_encrypted_data_string"; // 

    try {
      const decryptedData = this.decryptWithAES(encryptedData);
      console.log(':', decryptedData);
    } catch (error) {
      console.error('Error in exampleUsage:', error.message);
    }
  }
}