<template>
  <el-select
    v-model="value"
    filterable
    :placeholder="selectvalue.name"
    class="filtercom"
    popper-class="filtercomselect"
    multiple
    collapse-tags
    @change="changValue"
    v-loading="loading"
    :clearable="true"
    :filterMethod="filterMethod"
  >
    <el-option
      v-for="item in filteredOptions"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    ></el-option>
  </el-select>
</template>

<script>
import api from "@/api/api";

export default {
  props: ["selectvalue"],
  data() {
    return {
      originalOptions: [], // 
      options: [],
      value: [],
      loading: false,
      searchQuery: "", // 
    };
  },
  methods: {
    filterMethod(query) {
      this.searchQuery = query; // 
      console.log(this.searchQuery)
      this.filterOptions(); // 
    },
    changValue() {
      this.$emit("selectval", this.value);
    },
    getFilterdata() {
      this.loading = true;
      api.filterDeviceinfo({ key: this.selectvalue.value }).then((res) => {
        console.log(res.data);

        let processedData = res.data.data.map((item) => {
          if (this.selectvalue.name == "") {
            return {
              label: item == 1 ? "" : item == 2 ? "" : "",
              value: item,
            };
          } else {
            return {
              label: item,
              value: item,
            };
          }
        });

        this.originalOptions = [...processedData]; // 
        this.options = [...processedData];

        this.loading = false;
      });
    },
    filterOptions() {
      if (!this.searchQuery) {
        this.options = [...this.originalOptions];
        return;
      }

      const query = this.searchQuery.toLowerCase();
      this.options = this.originalOptions.filter(option =>
        option.label.toString().toLowerCase().includes(query)
      );
      console.log(this.searchQuery,this.options)
    },
  },
  mounted() {
    this.getFilterdata();
  },
  computed: {
    device_change() {
      return this.$store.state.device_change;
    },
    filteredOptions() {
      // 
      return this.options;
    }
  },
  watch: {
    device_change() {
      this.getFilterdata();
    },
    searchQuery(newVal) {
      this.filterOptions();
    },
    value(newVal) {
      // 
      if (newVal.length > 0) {
        this.searchQuery = "";
        this.filterOptions();
      }
    }
  }
};
</script>