<template>
    <div class="introdunction">
        <div class="base_list highlight" style="padding:10px">
            <!-- <el-tabs v-model="activeName" type="card" style="height: 100%">
            <el-tab-pane label="" name="first" style="height: 100%"> -->
            <el-radio-group v-model="activeName">
                <el-radio-button label=""> </el-radio-button>
                <el-radio-button label=""></el-radio-button>
            </el-radio-group>
            <div v-if="activeName == ''">
                <el-row>
                    <el-col :span="6" v-for="(camera, index) in cameras" :key="index"
                        style="height: 300px; width: 500px">
                        <VideoBox class="video-player" ref="'videoBox'" :muted="true" :mode="camera.mode"
                            :newId="camera.id" :data="camera" style="height: 300px; width: 500px" />
                    </el-col>
                </el-row>
            </div>

            <div v-if="activeName == ''">
                <el-row>
                    <el-col :span="6" v-for="(camera, index) in cameras" :key="index"
                        style="height: 300px; width: 500px">
                        <Backvideo class="video-player" :muted="true" ref="video2" :newId="camera.id" :data="camera"
                            style="height: 300px; width: 500px"></Backvideo>
                    </el-col>
                </el-row>
            </div>
            <div style="width: 50%; text-align: center; position: fixed; bottom: 0.1rem">
                <Navbar></Navbar>
            </div>
        </div>
    </div>
</template>

<script>
import VideoBox from "./components/VideoBox.vue";
import Backvideo from "./components/backvideo.vue";
import { queryVideoDevice } from "@/api/watch/video/videodevice.js";
import { queryVideoSite } from "@/api/watch/video/videosite.js";
import Navbar from "../components/Navbar";
import { getHKData, getRTSPData } from "@/utils/hkconfig";
import axios from "axios";
let array = [];
for (let index = 0; index < 20; index++) {
    array.push({
        title: "1-" + index,
        date: "2024-09-30 13:54",
    });
}
export default {
    components: { VideoBox, Backvideo, Navbar },
    data() {
        return {
            activeName: "",
            array: array,
            videoSite: 1, //
            cameras: [
                {
                    //   mode: "preview", //
                    //   id: "divGaoluTG", //id
                    //   ip: "200.150.100.252",
                    //   userName: "admin",
                    //   passWord: "ZG20241019", //
                    //   rtspPort: 554,
                    //   transStream: true, //
                    //   port: 80, //
                    //   channelID: 1, //
                    //   loadingTime: "3000",
                    //   streamType: 1,
                    //   videoWidth: 800,
                    //   videoHeight: 400,
                    //   title: "",
                    id: "01",
                    ip: "200.150.100.252",
                    userName: "admin",
                    passWord: "ZG20241019", //
                    rtspPort: 554,
                    transStream: false, //
                    port: 80, //
                    channelID: 1, //
                    loadingTime: "3000",
                    streamType: 1,
                    analogMode: false,
                    videoHeight: 100,
                    title: "camera 01",
                    mode: "preview", //
                    videoWidth: 500,
                    videoHeight: 300,
                },
                // {
                //     id: "02",
                //   ip: "200.150.100.252",
                //   userName: "admin",
                //   passWord: "ZG20241019", //
                //   rtspPort: 554,
                //   transStream: false, //
                //   port: 80, //
                //   channelID: 1, //
                //   loadingTime: "3000",
                //   streamType: 1,

                //   videoHeight: 100,
                //   title: "camera 01",
                //   mode: "preview", //
                //   videoWidth: 500,
                //   videoHeight: 300,
                // },
            ], //
        };
    },
    methods: {
        updateVideoDvices(rows) {
            var vdList = rows;
            // console.log(rows);
            this.cameras = []; //
            for (var i = 0; i < vdList.length; i++) {
                var videoTitle = vdList[i].name; //
                var parameterString = vdList[i].parameterString; //
                var protocol = vdList[i].protocol; //
                this.cameras = []; //
                var camera = { id: vdList[i].id + "", siteId: vdList[i].siteId }; //idid
                this.cameras.push(camera);
                try {
                    var parameterdata = JSON.parse(parameterString);
                    console.log("parameter", parameterdata);
                    if (parameterdata) {
                        camera.protocol = protocol; //
                        if (protocol == "hkvideo") {
                            //
                            var hkData = getHKData(parameterdata);
                            camera.hkData = hkData;

                        } else if (protocol == "rtsp") {
                            var rtspData = getRTSPData(parameterdata);
                            camera.rtspData = rtspData;
                        }
                    }
                    console.log("read camera:", camera); //
                } catch (e) {
                    console.warn(
                        "parse video device " + videoTitle + " :" + e.message
                    );
                }
            }
        },
    },
    mounted() {
        console.log(this.$route.fullPath.split("/"));
        if (this.$route.fullPath.split("/").length) {
            let siteId =
                this.$route.fullPath.split("/")[
                this.$route.fullPath.split("/").length - 1
                ];
            queryVideoDevice({
                pageNum1: 1,
                pageSize: 10000,
                siteId: siteId,
            }).then((res) => {
                console.log("res.rows", res.rows);
                // res.code = 401;
                if (res.code != 200) {
                    this.$message({
                        type: "warning",
                        message: ",",
                        //   duration: 0,
                        showClose: true,
                    });
                    setTimeout(() => {
                        this.$router.push("/");
                    }, 2000);
                } else {
                    //
                    this.updateVideoDvices(res.rows);
                }
            });
        }
    },
    watch: {
        activeIndex(newvalue, oldvalue) {
            if (newvalue.split("/").length) {
                let siteId = newvalue.split("/")[newvalue.split("/").length - 1];
                queryVideoDevice({
                    pageNum1: 1,
                    pageSize: 10000,
                    siteId: siteId,
                }).then((res) => {
                    console.log(res.rows);
                    if (res.code != 200) {
                        this.$message({
                            type: "warning",
                            message: ",",
                            //   duration: 0,
                            showClose: true,
                        });
                        setTimeout(() => {
                            this.$router.push("/");
                        }, 2000);
                    } else {
                        //
                        this.updateVideoDvices(res.rows);
                    }
                });
            }
        },
    },
    computed: {
        activeIndex() {
            return this.$route.path;
        },
    },
};
</script>

