<template>
    <div>
      <div id="mapContainer" style="width: 100%; height: 500px;"></div>
    </div>
  </template>
  
  <script>
  export default {
    mounted() {
      this.initMap(); // 
    },
    methods: {
      initMap() {
        // 
        const map = new AMap.Map("mapContainer", {
          zoom: 15, // 
          center: [116.397428, 39.90923], // 
          mapStyle: "amap://styles/whitesmoke" // 
        });
  
        //  AR 
        AMap.plugin(["AMap.AR"], () => {
          const arControl = new AMap.AR({
            position: "RB", // RB
            viewMode: "3D", //3D
          });
  
          //  AR 
          map.addControl(arControl);
          arControl.start(); //  AR 
        });
      },
    },
  };
  </script>
  
  <style scoped>
  #mapContainer {
    width: 100%;
    height: 500px;
  }
  </style>
  