<template>
    <div>
      <div class="mb8">
        <div
          v-loading="loading"
          :id="myNewId"
          class="plugin"
          style="width: 500px; height: 300px"
        ></div>
        <el-button type="primary" size="mini" @click="clickStopPlayback"
          ></el-button
        >
        <!-- <el-button type="primary" size="mini" @click="clickReversePlayback"
            ></el-button
          > -->
        <el-button type="primary" size="mini" @click="clickPause"></el-button>
        <el-button type="primary" size="mini" @click="clickResume"
          ></el-button
        >
        <el-button type="primary" size="mini" @click="clickPlayFast"
          ></el-button
        >
        <el-button type="primary" size="mini" @click="clickPlaySlow"
          ></el-button
        >
        <el-button type="primary" size="mini" @click="clickFullScreen"
          ></el-button
        >
  
        <el-row style="margin-top: 10px">
          <div style="float: left">
            <el-date-picker
              style="margin-left: 3px; width: 6rem"
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="timeRange"
              type="datetimerange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              @change="getTimeRange"
              :append-to-body="false"
            />
            <el-button
              type="primary"
              icon="el-icon-search"
              @click="clickRecordSearch()"
              ></el-button
            >
          </div>
          <el-table
            bordered
            row-class-name="rowstyle"
            style="width: 100%; background: transparent; minheight: 4rem"
            :data="searchList"
            @selection-change="handleSelectionChange"
            v-loading="tableloading"
            height="4rem"
          >
            <el-table-column
              label=""
              align="left"
              key="index"
              prop="index"
              :show-overflow-tooltip="true"
            />
            <el-table-column
              label=""
              align="left"
              :show-overflow-tooltip="true"
            >
              <template slot-scope="scope">
                {{ scope.row.startTime }}
              </template>
            </el-table-column>
            <el-table-column
              label=""
              align="left"
              :show-overflow-tooltip="true"
            >
              <template slot-scope="scope">
                {{ scope.row.endTime }}
              </template>
            </el-table-column>
            <el-table-column
              label=""
              align="left"
              :show-overflow-tooltip="true"
            >
              <template slot-scope="scope">
                {{ scope.row.fileName }}
              </template>
            </el-table-column>
            <el-table-column
              label=""
              align="center"
              class-name="small-padding fixed-width"
            >
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="text"
                  icon="el-icon-view"
                  @click="handlePlayBack(scope.row)"
                  >...</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-row>
      </div>    
    </div>
  </template>
  
  <script>
  export default {
    name: "BackVideo", //
    inheritAttrs: false,
    props: {
      data: {
        type: Object || Array,
        require: true,
      },
      mode_info: {
        type: String,
        require: true,
      },
      newId: {
        type: String,
        require: true,
        default: "divABC02",
      },
    },
    watch: {
       newId: {
         deep: true,
         immediate: true,
         handler(newVal, oldVal) {
            if (newVal) {
              this.myNewId = 'divPlayBack' + newVal;
            }
          },
       },
       data: {
          deep: true,
          immediate: true,
          handler(newVal, oldVal) {
             console.log('backvideo:oldVal', oldVal);
             console.log('backvideo:newVal', newVal);
             if (newVal && newVal.hkData) {
               var hkData = newVal.hkData;
               this.ip = hkData.nvrAddress; //nvr
               this.myNewId = "divPlayBack" + newVal.id;
               this.userName = hkData.userName; //nvr
               this.passWord = hkData.passWord; //
               if (hkData.rtspPort) 
                  this.rtspPort = hkData.rtspPort;
               else this.rtspPort = 554; //
               if (hkData.transStream) 
                  this.transStream = true;
               else 
                  this.transStream = false;
               if (hkData.port) 
                  this.port = hkData.port; //
               else this.port = 80; //80
               this.iChannelID = !hkData.channelID ? 1 : hkData.channelID; //ID
               if (hkData.title) 
                  this.title = hkData.title; //
               else 
                  this.title = "";
               if (hkData.streamType) 
                  this.streamType = hkData.streamType;
               else this.streamType = 1;
               
               if(this.ip){
                   var t = this.loadingTime / 1000;
                   setTimeout(() => {
                   this.initS(); //
                   }, t);
               }
             }
          },
        },
    },
    data() {
      return {
        tableloading: false,
        mode: this.mode_info,
        imgdialog: false,
        myNewId: "divBackVideo",
        visible: true, //
        showSearch: true, //
        advanceMode: true, //
        queryParams: {}, //
        ip: undefined, //IP
        transStream: true, //
        rtspPort: 554, //rtsp
        userName: "", //admin
        passWord: "", //
        port: 80, //80
        iChannelID: 1, //id
        windowCount: 1, //1X1
        loadingTime: 2000, //
        timeOut: 2000, //
        videoWidth: 500, //
        videoHeight: 300, //
        fullSize: false, //
        title: "camera01", //
        startTime: "", //
        endTime: "", //
        g_iWndIndex: 0, //k
        timeRange: [], //
        streamType: 1, //1, 
        bZeroChannel: false, //
        streamTypeList: [
          { id: 1, label: "" },
          { id: 2, label: "" },
          { id: 3, label: "" },
          { id: 4, label: "" },
        ],
        startTime: "",
        endTime: "",
        g_iSearchTimes: 0, //
        currentRow: {}, //
        searchList: [], //
        currentRow: {}, //
        plugInInitialized: false, //
        loading: true,
      };
    },
    mounted() {
      console.log("webSDKPlugin/HCWebSDKPlugin.exe");
      this.timeRange = [this.getStartDate(), this.getEndDate()];
      this.getTimeRange();
      if(this.ip){
         this.initS(); //
      }    
    },
    methods: {
      closeModal() {
        this.imgdialog = false;
        this.breakDom();
      },
      handleSelectionChange(row) {
        this.currentRow = row;
        console.log("currentRow", this.currentRow);
      },
      // 
      handlePlayBack(row) {
        // 
        this.imgdialog = true;
        this.currentRow = row;
  
        // 
        const szDeviceIdentify = `${this.ip}_${this.port}`;
        const playbackURI = row.playbackURI; // 
        const szStartTime = row.startTime0; // 
        const szEndTime = row.endTime0; // 
  
        // 
        const iWndIndex = this.g_iWndIndex || 0; // 
        const iRtspPort = this.rtspPort; // RTSP
        const iStreamType = this.streamType || 1; // 
        const bZeroChannel = this.bZeroChannel; // 
        const bTransStream = this.transStream; // 
  
        // 
        if (bZeroChannel) {
          console.warn("");
          this.$message.warning("");
          return;
        }
  
        // 
        const startPlayback = () => {
          const options = {
            iWndIndex,
            szUrl: playbackURI,
            szStartTime,
            szEndTime,
            iPort: iRtspPort,
            iStreamType,
            success: () => {
              console.log(`${szDeviceIdentify} `);
              // this.$message.success("");
            },
            error: (oError) => {
              console.error(
                `${szDeviceIdentify} `,
                oError.errorCode,
                oError.errorMsg
              );
              this.$message.error(`${oError.errorMsg || ""}`);
            },
          };
  
          console.log("", options);
          WebVideoCtrl.I_StartPlay(szDeviceIdentify, options);
        };
  
        // 
        const oWndInfo = WebVideoCtrl.I_GetWindowStatus(iWndIndex);
        if (oWndInfo) {
          // 
          WebVideoCtrl.I_Stop({
            iWndIndex,
            success: () => {
              console.log("...");
              startPlayback();
            },
            error: (oError) => {
              console.error("", oError.errorCode, oError.errorMsg);
              this.$message.error("");
            },
          });
        } else {
          console.log("...");
          startPlayback();
        }
      },
  
      getTimeRange() {
        if (this.timeRange.length) {
          this.queryParams.startTime = this.timeRange[0];
          this.queryParams.endTime = this.timeRange[1];
        } else {
          this.queryParams.startTime = this.getStartDate();
          this.queryParams.endTime = this.getEndDate();
        }
      },
      loadXML(szXml) {
        if (null == szXml || "" == szXml) {
          return null;
        }
  
        var oXmlDoc = null;
  
        if (window.DOMParser) {
          var oParser = new DOMParser();
          oXmlDoc = oParser.parseFromString(szXml, "text/xml");
        } else {
          oXmlDoc = new ActiveXObject("Microsoft.XMLDOM");
          oXmlDoc.async = false;
          oXmlDoc.loadXML(szXml);
        }
  
        return oXmlDoc;
      },
      toXMLStr(oXmlDoc) {
        var szXmlDoc = "";
  
        try {
          var oSerializer = new XMLSerializer();
          szXmlDoc = oSerializer.serializeToString(oXmlDoc);
        } catch (e) {
          try {
            szXmlDoc = oXmlDoc.xml;
          } catch (e) {
            return "";
          }
        }
        if (szXmlDoc.indexOf("<?xml") == -1) {
          szXmlDoc = "<?xml version='1.0' encoding='utf-8'?>" + szXmlDoc;
        }
        return szXmlDoc;
      },
      encodeString(str) {
        if (str) {
          return str
            .replace(/&/g, "&amp;")
            .replace(/</g, "&lt;")
            .replace(/>/g, "&gt;");
        } else {
          return "";
        }
      },
      handleXXX() {
        var szDeviceIdentify = this.ip;
        this.$emit("desPlug", szDeviceIdentify);
      },
      async initS() {
        let that = this;
        console.log("", this);
        this.loading = true;
        //   if (!that.newId) {
        //     console.warn("hk video id empty, can not create hk video plugin.");
        //     return;
        //   }
        //   if (!that.ip || !that.port) {
        //     console.warn("hk video ip/port empty, can not init plugin.");
        //     return;
        //   }
        if (!this.plugInInitialized) {
          console.log("");
          // 
          await WebVideoCtrl.I_InitPlugin({
            bWndFull: true, // true: false:
            iWndowType: 1, //  1 1*1   22*2
            cbSelWnd: function (xmlDoc) {
              //
              this.g_iWndIndex = parseInt(
                $(xmlDoc).find("SelectWnd").eq(0).text(),
                10
              );
              var szInfo = "" + this.g_iWndIndex;
              console.log(szInfo);
            },
            cbDoubleClickWnd: function (iWndIndex, bFullScreen) {
              this.iWndIndex = iWndIndex;
              var szInfo = "" + iWndIndex;
              if (!bFullScreen) {
                szInfo = "" + iWndIndex;
              }
              console.log(szInfo);
            },
            cbEvent: function (iEventType, iParam1, iParam2) {
              console.log(
                ", eventType=" +
                  iEventType +
                  ",param1=" +
                  iParam1 +
                  ",p2=" +
                  iParam2
              );
              if (2 == iEventType) {
                // 
                console.log("" + iParam1 + "");
              } else if (-1 == iEventType) {
                console.log("" + iParam1 + "");
              } else if (3001 == iEventType) {
                this.clickStopRealPlay();
                //this.clickStopRecord(g_szRecordType, iParam1);
              }
            },
            cbInitPluginComplete: function () {
              that.plugInInitialized = true;
              console.log('pluginitialized:', that.plugInInitialized);
  
              WebVideoCtrl.I_InsertOBJECTPlugin(that.myNewId).then(
                () => {
                  console.log("init ok.");
                  // 
                  WebVideoCtrl.I_CheckPluginVersion().then((bFlag) => {
                    if (bFlag) {
                      let str =
                        "HCWebSDKPlugin.exe";
                      that.$message.error(str);
                      console.log(str);
                    }
                  });
                },
                (err) => {
                  console.log("err", err);
                  let str1 =
                    "HCWebSDKPlugin.exe";
                  that.$message.error(str1);
                  console.log(str1);
                }
              );
              this.loading = false;
            },
          });
        }
        setTimeout(() => {
          // let cw = Math.round(document.body.clientWidth / 1920);
          // let ch = Math.round(document.body.clientHeight / 1080);
          // let width = parseInt(this.videoWidth * cw, 10);
          // let height = parseInt(this.videoHeight * ch, 10);
          // if (height <= 200) {
          //   height = 200;
          // }
          // if (height <= 200) {
          //   height = 200;
          // }
          // //let w1 = (window.innerWidth - document.getElementById(this.newId).offsetLeft) - 1500;
          // //let w2 = document.getElementById(this.newId).clientHeight;
          // console.log("00000===>", width, height);
          // // 
          // WebVideoCtrl.I_Resize(width, height);
          this.setVideoWindowResize(this.myNewId, "500", "300");
          this.clickLogin();
        }, 500);
      },
      // windowchange
      setVideoWindowResize(pid, width, height) {
        if (!pid) return;
        var elment = document.getElementById(pid);
        if (elment) {
          elment.style.width = width + "px";
          elment.style.height = height + "px";
        }
      },
      // 
      clickLogin() {
        let that = this;
        that.g_iSearchTimes = 0;
        that.searchList = [];
        let hasLogin = false;
        var szDeviceIdentify = this.ip + "_" + this.port;
        const iRet = WebVideoCtrl.I_Login(
          this.ip,
          this.windowCount,
          this.port,
          this.userName,
          this.passWord,
          {
            timeout: this.timeOut,
            success: function (xmlDoc) {
              console.log(szDeviceIdentify + " ");
              setTimeout(function () {
                setTimeout(function () {
                  //that.getChannelInfo();
                }, 1000);
                that.getDevicePort();
              }, 10);
            },
            error: function (err) {
              console.log("-err===>", err);
              if (err.errorCode == 2001) {
                hasLogin = true;
                console.log(szDeviceIdentify + " ");
                console.log("do start realplay...");
                  // 
                that.getDevicePort();
              }
              else{
                that.$message.error("/");
              }
            },
          }
        );
  
        if (iRet === -1) {
          console.log(szDeviceIdentify + " ");
        }
      },
  
      // 
      getDevicePort() {
        if (null == this.ip) {
          return;
        }
        WebVideoCtrl.I_GetDevicePort(this.ip).then((oPort) => {
          console.log("152", oPort);
          // channel
          this.getChannelInfo();
        });
      },
      // 
      async getChannelInfo() {
        let that = this;
        var szDeviceIdentify = this.ip + "_" + this.port;
        if (null == szDeviceIdentify) {
          return;
        }
        console.log("szDeviceIdentify==============>", szDeviceIdentify);
        // 
        WebVideoCtrl.I_GetAnalogChannelInfo(szDeviceIdentify, {
          success: function (xmlDoc) {
            that.autoStartPlayback();
            console.log(xmlDoc);
          },
          error: function (oError) {
            console.log(
              szDeviceIdentify + "",
              oError.errorCode,
              oError.errorMsg
            );
          },
        });
  
        // 
        WebVideoCtrl.I_GetDigitalChannelInfo(szDeviceIdentify, {
          success: function (xmlDoc) {
            console.log(xmlDoc);
            that.autoStartPlayback();
          },
          error: function (oError) {
            console.log(
              szDeviceIdentify + " ",
              oError.errorCode,
              oError.errorMsg
            );
          },
        });
      },
      // 
      clickStartRealPlay(iStreamType) {
        let that = this;
        var oWndInfo = WebVideoCtrl.I_GetWindowStatus(this.g_iWndIndex) || null;
        if (!this.ip || !this.port) return;
        var szDeviceIdentify = this.ip + "_" + this.port;
        if ("undefined" === iStreamType) {
          this.streamType = 1;
        } else {
          this.streamType = iStreamType;
        }
        if (null == szDeviceIdentify) {
          return;
        }
        var startRealPlay = function () {
          WebVideoCtrl.I_StartRealPlay(szDeviceIdentify, {
            iStreamType: that.streamType,
            iChannelID: that.iChannelID,
            bZeroChannel: that.bZeroChannel,
            success: function () {
              WebVideoCtrl.I_Logout(szDeviceIdentify);
              console.log("");
            },
            error: function (oError) {
              that.$message.error(szDeviceIdentify + "");
              console.log(szDeviceIdentify + "");
              console.log("oError", oError);
            },
          });
        };
  
        if (oWndInfo != null) {
          // 
          WebVideoCtrl.I_Stop({
            success: function () {
              startRealPlay();
            },
          });
        } else {
          startRealPlay();
        }
      },
      // 
      clickStopRealPlay() {
        WebVideoCtrl.I_Stop({
          success: function () {
            console.log("");
          },
          error: function (oError) {
            console.log(" ");
          },
        });
      },
  
      // 
      async clickCapturePic(iChannelID) {
        var oWndInfo = WebVideoCtrl.I_GetWindowStatus(this.g_iWndIndex),
          szInfo = "";
  
        if (oWndInfo != null) {
          var oLocalConfig = await WebVideoCtrl.I_GetLocalCfg();
          var szCaptureFileFormat = "0";
          if (oLocalConfig) {
            szCaptureFileFormat = oLocalConfig.captureFileFormat;
          }
  
          var szChannelID = iChannelID;
          var szPicName =
            oWndInfo.szDeviceIdentify +
            "_" +
            szChannelID +
            "_" +
            new Date().getTime();
          //"playback_"
          if ("playback" === mode) {
            szPicName =
              "playback_" +
              oWndInfo.szDeviceIdentify +
              "_" +
              szChannelID +
              "_" +
              new Date().getTime();
          }
  
          szPicName += "0" === szCaptureFileFormat ? ".jpg" : ".bmp";
  
          WebVideoCtrl.I_CapturePic(szPicName, {
            bDateDir: true, //
          }).then(
            function () {
              szInfo = "";
              console.log(oWndInfo.szDeviceIdentify + " " + szInfo);
            },
            function (oError) {
              szInfo = " ";
              console.log(
                oWndInfo.szDeviceIdentify + szInfo,
                oError.errorCode,
                oError.errorMsg
              );
            }
          );
        }
      },
      // 
      clickCapturePicData() {
        var oWndInfo = WebVideoCtrl.I_GetWindowStatus(this.g_iWndIndex),
          szInfo = "";
        if (oWndInfo != null) {
          WebVideoCtrl.I_CapturePicData()
            .then(function (data) {
              console.log(data);
              szInfo = "";
              console.log(oWndInfo.szDeviceIdentify + " " + szInfo);
            })
            .catch(() => {
              szInfo = "";
              console.log(oWndInfo.szDeviceIdentify + " " + szInfo);
            });
        }
      },
  
      // 
      clickRecordSearch(iType) {
        if (this.queryParams.startTime)
          this.startTime = this.queryParams.startTime;
        else this.startTime = this.getStartDate();
        if (this.queryParams.endTime)
          // this.endTime = this.getDateString(this.queryParams.endTime);
          this.endTime = this.queryParams.endTime;
        else this.endTime = this.getEndDate();
        var that = this;
        console.log("starttime", this.startTime);
        console.log("endTime", this.endTime);
        var szDeviceIdentify = this.ip + "_" + this.port,
          iChannelID = this.iChannelID,
          iStreamType = !this.streamType ? 1 : this.streamType, //
          szStartTime = this.startTime,
          szEndTime = this.endTime;
        if (
          Date.parse(szEndTime.replace(/-/g, "/")) -
            Date.parse(szStartTime.replace(/-/g, "/")) <
          0
        ) {
          ("");
          return;
        }
        if (null == szDeviceIdentify) {
          return;
        }
  
        if (this.bZeroChannel) {
          // 
          return;
        }
        if (!iType) {
          that.g_iSearchTimes = 0;
          that.searchList = [];
          that.currentRow = {}; //
        }
        console.log("g_iSearchTimes", that.g_iSearchTimes);
        console.log("recordcount", that.searchList.length);
        that.loading = true;
        //console.log("", iChannelID, szStartTime, szEndTime);
        that.tableloading = true;
        //UTC
        WebVideoCtrl.I_RecordSearch(
          szDeviceIdentify,
          iChannelID,
          szStartTime,
          szEndTime,
          {
            iStreamType: iStreamType,
            iSearchPos: that.g_iSearchTimes,
            success: function (xmlDoc) {
              console.log("xmlDoc", xmlDoc);
              console.log(
                "responseStatusStrg",
                $(xmlDoc).find("responseStatusStrg")
              );
              if ("MORE" === $(xmlDoc).find("responseStatusStrg").eq(0).text()) {
                for (
                  var i = 0, nLen = $(xmlDoc).find("searchMatchItem").length;
                  i < nLen;
                  i++
                ) {
                  var szPlaybackURI = $(xmlDoc).find("playbackURI").eq(i).text();
                  var myStartTime = $(xmlDoc).find("startTime").eq(i).text();
                  var myEndTime = $(xmlDoc).find("endTime").eq(i).text();
                  var myFileName = szPlaybackURI.substring(
                    szPlaybackURI.indexOf("name=") + 5,
                    szPlaybackURI.indexOf("&size=")
                  );
  
                  //
                  var searchItem = {
                    index: that.g_iSearchTimes + 1,
                    startTime0: myStartTime,
                    endTime0: myEndTime,
                    startTime: myStartTime.replace("T", " ").replace("Z", ""),
                    endTime: myEndTime.replace("T", " ").replace("Z", ""),
                    fileName: myFileName,
                    playbackURI: szPlaybackURI,
                  };
                  that.searchList.push(searchItem);
                  that.g_iSearchTimes++;
                }
                that.clickRecordSearch(1); // 
              } else if (
                "OK" === $(xmlDoc).find("responseStatusStrg").eq(0).text()
              ) {
                var iLength = $(xmlDoc).find("searchMatchItem").length;
                console.log("iLength", iLength);
                for (var i = 0; i < iLength; i++) {
                  var szPlaybackURI = $(xmlDoc).find("playbackURI").eq(i).text();
                  var myStartTime = $(xmlDoc).find("startTime").eq(i).text();
                  var myEndTime = $(xmlDoc).find("endTime").eq(i).text();
                  var myFileName = szPlaybackURI.substring(
                    szPlaybackURI.indexOf("name=") + 5,
                    szPlaybackURI.indexOf("&size=")
                  );
                  //
                  var searchItem = {
                    index: that.g_iSearchTimes + 1,
                    startTime: myStartTime.replace("T", " ").replace("Z", ""),
                    endTime: myEndTime.replace("T", " ").replace("Z", ""),
                    startTime0: myStartTime,
                    endTime0: myEndTime,
                    fileName: myFileName,
                    playbackURI: szPlaybackURI,
                  };
                  that.searchList.push(searchItem);
                  that.g_iSearchTimes++;
                }
                //that.clickRecordSearch(1); // 
                console.log(szDeviceIdentify + " ");
              } else if (
                "NO MATCHES" === $(xmlDoc).find("responseStatusStrg").eq(0).text()
              ) {
                //that.searchList = [];
                //that.g_iSearchTimes = 0;
                setTimeout(function () {
                  console.log(szDeviceIdentify + " ");
                }, 50);
              }
              that.tableloading = false;
            },
            error: function (oError) {
              console.log(
                szDeviceIdentify + " ",
                oError.errorCode,
                oError.errorMsg
              );
  
              that.tableloading = false;
            },
          }
        );
      },
      getDateString(date) {
        const yesterday = new Date(date);
        //yesterday.setDate(yesterday.getDate() - 1);
        const year = yesterday.getFullYear();
        const month = ("0" + (yesterday.getMonth() + 1)).slice(-2);
        const day = ("0" + yesterday.getDate()).slice(-2);
        return `${year}-${month}-${day} 00:00:00`;
      },
      getStartDate() {
        const today = new Date();
        const yesterday = new Date(today);
        //yesterday.setDate(yesterday.getDate() - 1);
        const year = yesterday.getFullYear();
        const month = ("0" + (yesterday.getMonth() + 1)).slice(-2);
        const day = ("0" + yesterday.getDate()).slice(-2);
        return `${year}-${month}-${day} 00:00:00`;
      },
      getStartDateZ() {
        const today = new Date();
        const yesterday = new Date(today);
        //yesterday.setDate(yesterday.getDate() - 1);
        const year = yesterday.getFullYear();
        const month = ("0" + (yesterday.getMonth() + 1)).slice(-2);
        const day = ("0" + yesterday.getDate()).slice(-2);
        return `${year}${month}${day}T000000Z`;
      },
      getEndDate() {
        const today = new Date();
        const yesterday = new Date(today);
        //yesterday.setDate(yesterday.getDate() - 1);
        const year = yesterday.getFullYear();
        const month = ("0" + (yesterday.getMonth() + 1)).slice(-2);
        const day = ("0" + yesterday.getDate()).slice(-2);
        return `${year}-${month}-${day} 23:59:59`;
      },
      getEndDateZ() {
        const today = new Date();
        const yesterday = new Date(today);
        //yesterday.setDate(yesterday.getDate() - 1);
        const year = yesterday.getFullYear();
        const month = ("0" + (yesterday.getMonth() + 1)).slice(-2);
        const day = ("0" + yesterday.getDate()).slice(-2);
        return `${year}${month}${day}T235959Z`;
