      autoStartPlayback() {
        //
        var iWndIndex = 0;
        if (this.g_iWndIndex) iWndIndex = this.g_iWndIndex;
        var oWndInfo = WebVideoCtrl.I_GetWindowStatus(iWndIndex),
          szDeviceIdentify = this.ip + "_" + this.port,
