        var szInfo = "",
          bChecked = this.transStream, //
          iRet = -1;
        //console.log('start play back...');
        var szStartTime, szEndTime;
        if (null == szDeviceIdentify) {
          return;
        }
  
        if (bZeroChannel) {
          // 
          console.warn("zero channel, can not play back.");
          return;
        }
  
        szStartTime = this.getStartDate();
        szEndTime = this.getEndDate();
  
        var that = this;
        var startPlayback = function () {
          if (that.transStream) {
            // that.transStream 
            var oTransCodeParam = {
              TransFrameRate: "14", // 051627486981010111212161415151813201622
              TransResolution: "1", // 255Auto34CIF2QCIF1CIF
              TransBitrate: "19", // 232K348K464K580K696K7128K8160K9192K10224K11256K12320K13384K14448K15512K16640K17768K18896K191024K201280K211536K221792K232048K243072K254096K268192K
            };
            var options = {
              iWndIndex: iWndIndex,
              iStreamType: iStreamType,
              iChannelID: that.iChannelID,
              szStartTime: szStartTime,
              szEndTime: szEndTime,
  
              oTransCodeParam: oTransCodeParam,
              success: function () {
                szInfo = "";
                console.log(szDeviceIdentify + " " + szInfo);
              },
              error: function (oError) {
                szInfo = "";
                console.log(
                  szDeviceIdentify + szInfo,
                  oError.errorCode,
                  oError.errorMsg
                );
              },
            };
            console.log("playback options:", options);
            WebVideoCtrl.I_StartPlayback(szDeviceIdentify, options);
          } else {
            var options = {
              iWndIndex: iWndIndex,
              iStreamType: iStreamType,
              iChannelID: that.iChannelID,
              szStartTime: szStartTime,
              szEndTime: szEndTime,
              success: function () {
                szInfo = "";
                console.log(szDeviceIdentify + " " + szInfo);
