<template>
  <el-row type="flex" justify="end">
    <el-col
      :span="2"
      v-for="item in show_filterinfo"
      :key="item.name"
      :style="{
        minWidth:
          item.name == ''
            ? '1.5rem'
            : item.name.indexOf('') >= 0
            ? '1.94rem'
            : '1.97rem',
      }"
    >
      <!-- <span>{{ item.name }}</span> -->
      <component
        :is="item.type"
        style="margin: 0 0.05rem; width: calc(100% - 0.1rem)"
        :selectvalue="item"
        @selectval="(data) => selectvalFunc(data, item.value)"
        :disabled="disabled"
      ></component>
    </el-col>
    <el-button
      type="primary"
      @click="searchFunc"
      icon="el-icon-search"
      class="plusicon"
      style="float: right"
      ></el-button
    >
  </el-row>
</template>

<script>
import Filterdate from "./filterdate.vue";
import Filterselect from "./filterselect.vue";

export default {
  props: ["filterinfo", "disabled"],
  data() {
    return {
      all_filterinfo: [
        { name: "", type: "filterselect", value: "org" },
        { name: "", type: "filterselect", value: "workshop" },
        { name: "", type: "filterselect", value: "install_location" },
        { name: "", type: "filterselect", value: "device_name" },
        { name: "", type: "filterselect", value: "device_status" },
        { name: "", type: "filterdate", value: "check_date" },
        { name: "", type: "filterdate", value: "next_check_date" },
        // { name: "", type: "filterdate" },
      ],
      show_filterinfo: [],
      filterparams: {},
    };
  },
  components: { Filterselect, Filterdate },
  mounted() {
    this.show_filterinfo = this.all_filterinfo.filter(
      (item) => this.filterinfo.indexOf(item.name) >= 0
    );
  },
  methods: {
    searchFunc() {
      this.$emit("searchFunc");
    },
    isObjectEmpty(obj) {
      return Object.keys(obj).every(
        (key) => obj[key] == null || obj[key].length == 0
      );
    },
    selectvalFunc(selectval, name) {
      this.filterparams[name] = selectval;
      console.log(this.isObjectEmpty(this.filterparams));
      if (this.isObjectEmpty(this.filterparams)) {
        this.filterparams = {};
      }
    },
  },
};
</script>

<style></style>
