        </div>
        <el-row justify="space-between">
            <el-col style="width: 2rem" v-if="!showSelect">
                <!-- <el-button
          type="primary"
          @click="openAddRowDialog"
          icon="el-icon-plus"
          class="plusicon"
          ></el-button
        > -->
            </el-col>
            <el-col :span="18">
                <el-popconfirm confirm-button-text="" cancel-button-text="" icon="el-icon-info" title=""
                    cancel-button-type="primary" @confirm="showSelectFunc('')" @cancel="showSelectFunc('')">
                    <el-button type="primary" class="plusicon" v-if="!showSelect" slot="reference" :disabled="loading">
                        <span></span>
                    </el-button>
                </el-popconfirm>
                <span style="color:white">1 2</span>
                <div v-if="showSelect" style="width: 5rem">
                    <el-button type="text" @click="confirmSelectFunc" class="plusicon" v-if="showSelect"
                        style="margin-right: 0.1rem">
                        <span></span>
                    </el-button>
                    <el-button type="text" @click="showSelectFunc('')" class="plusicon" v-if="showSelect"
                        style="margin-right: 0.1rem; color: grey">
                        <span></span>
                    </el-button>
                    <span class="report_msg">
                        <span style="font-weight: bold"> {{ messageinfo }} </span></span>
                </div>
            </el-col>
            <el-button v-if="!showsearch && !showSelect" type="primary" @click="
          showsearch = true;
          heightValue = 'calc(100vh - 3.8rem)';
        " icon="el-icon-search" class="plusicon" title="" style="float: right"></el-button>
            <el-col :span="24" v-else-if="!showSelect">
                <filtercom :filterinfo="[
            '',
            '',
            '',
            '',
            '',
            '',
            '',
          ]" ref="filtercom" @searchFunc="searchFunc"></filtercom>
            </el-col>
        </el-row>
        <div class="table-container">
            <el-table bordered row-class-name="rowstyle" :data="paginatedData"
                style="width: 100%; background: transparent" :cell-class-name="getCellClassName"
                :height="!showsearch ? 'calc(100vh - 3.3rem)' : 'calc(100vh - 3.3rem)'" v-loading="loading"
                :key="showSelect" @selection-change="handleSelectionChange" ref="multipleTable">
                <el-table-column label="" v-if="!showSelect" width="110rem">
                    <template #default="scope">
                        <el-button class="deletbtn" @click="confirmDeleteRow(scope.row)" type="text" size="small"
                            style="font-size: 0.175rem" :disabled="scope.row['ID'] == null">
                            
                        </el-button>
                        <el-button @click="editRow(scope.row)" type="text" size="small" style="font-size: 0.175rem"
                            :disabled="scope.row['ID'] == null">
                            
                        </el-button></br>
                        <el-button @click="openAddRowDialog(scope.row)" type="text" size="small"
                            style="font-size: 0.175rem; margin-left: 0; color: #00c93b">
                            
                        </el-button>
                        <el-button @click="viewhistory(scope.row)" type="text" size="small"
                            style="font-size: 0.175rem; color: rgb(212 158 78)">
                            
                        </el-button>
                    </template>
                </el-table-column>
                <el-table-column type="selection" width="55" v-if="showSelect">
                </el-table-column>
                <el-table-column label="">
                    <template #default="scope">
                        {{ scope.row["addFlag"] == 1 ? "" : "" }}
                    </template>
                </el-table-column>
                <el-table-column fit v-for="(column, index) in column" :key="index" :prop="column.prop"
                    :label="column.label" :sortable="column.sortable" v-if="column.label != 'ID'&& column.label != ''&&column.label != ''&&
              column.label != ''"    >
                    <template #default="scope">
                        <div v-if="!scope.row.editing">
                            <div v-if="
                  column.prop == '' ||
                  column.prop == '' ||
                  column.prop == ''||
                  column.prop == ''
                " @click="showbigimg(scope.row[column.prop])">
                                <img :src="scope.row[column.prop]" style="width: 0.5rem"
                                    v-if="scope.row[column.prop]" />
                                <span v-else></span>
                            </div>
                            <div v-if="column.prop == ''">
                                <img :src="
                    scope.row[column.prop] != 'grey'
                      ? require(`../../../assets/images/${
                          scope.row[column.prop]
                        }light.png`)
                      : require(`../../../assets/images/greenlight.png`)
                  " class="lightimage" :style="
                    scope.row[column.prop] != 'grey'
                      ? ''
                      : 'filter: grayscale(100%)'
                  " />
                            </div>
                            <span v-else-if="
                  column.prop != '' &&
                  column.prop != '' &&
                  column.prop != ''&&
                  column.prop != ''
                ">
                                <span v-if="column.prop.indexOf('') >= 0">{{
                                    String(scope.row[column.prop]).replace("T00:00:00", "")
                                    }}</span>
                                <span v-else>
                                    {{ scope.row[column.prop] }}
                                </span>
                            </span>
                        </div>

                        <!-- <div v-else>
                <el-input
                  v-model="scope.row[column.prop]"
                  
                ></el-input>
              </div> -->
                    </template>
                </el-table-column>
            </el-table>
            <el-button type="primary" @click="
          $downloadExcel({
            tableData: tabledata,
            title: '',
          })
        " class="plusicon" size="small"></el-button>
        </div>

        <el-dialog :title="operatetitle" :visible.sync="addRowDialogVisible" :modal="false" top="0.8rem"
            v-if="operate[2] == 'add'">
            <div class="dialog-content">
                <el-form :model="newRow" label-position="top" ref="newRow" :rules="rules_data">
                    <el-form-item v-for="(column, coindex) in columns" :key="column.prop"
                        :label="column.label!=''?column.label:(newRow['']==''?'':'')" :prop="column.prop"
                        v-if="
                        column.label != 'ID' &&
                        column.label != 'ID' &&
                        column.prop != '' &&
                        column.label != '' &&
                        column.label != ''&&
                        column.label != ''&&
                        column.label != ''&&
                        column.label != ''
                        ">
                        <div v-if="column.prop == ''">
                            <el-radio-group v-model="newRow[column.prop]"
                                :disabled="check_datas.indexOf(column.prop) >= 0">
                                <el-radio v-for="city in checkdata1" :label="city" :key="city">{{ city }}</el-radio>
                            </el-radio-group>
                        </div>
                        <el-select v-if="column.prop == ''" v-model="newRow[column.prop]" style="width: 8rem">
                            <el-option v-for="item in deviceKind" :label="item" :value="item" :key="item"></el-option>
                        </el-select>
                        <el-select v-model="newRow[column.prop]" v-if="column.prop == ''" style="width: 8rem"
                            @change="changeStatus">
                            <el-option v-for="item in deviceKind2" :label="item" :value="item" :key="item"></el-option>
                        </el-select>
                        <div v-if="column.prop == ''">
                            <Uploadocom v-if="!newRow[column.prop]" :uploadAction="'/api/function/upload'"
                                :showloading2="showloading2" @upload="listenUpload"
                                @imageshow="(data) => imageshowFunc(data, column.prop)" ref="uploadocom"></Uploadocom>
                            <div style="width: 1rem" v-else>
                                <img :src="newRow[column.prop]" style="width: 1rem; height: 1rem" />
                                <div style="width: 1rem">
                                    <span style="color: #247cc3; cursor: pointer"
                                        @click="cancelImg(column.prop)"></span>
                                </div>
                            </div>
                        </div>

                        <el-date-picker v-if="column.prop.indexOf('') >= 0" v-model="newRow[column.prop]" type="date"
                            placeholder="" value-format="yyyy-MM-dd">
                        </el-date-picker>
                        <el-input
                            v-else-if="column.prop != '' && column.prop != ''&&column.prop != ''&&column.prop != ''&&column.prop != ''"
                            v-model="newRow[column.prop]" :placeholder="'' + column.label" style="width: 8rem"
                            :disabled="check_datas.indexOf(column.prop) >= 0"></el-input>
                        <el-input v-if="newRow['']==''&&column.prop == ''" v-model="newRow[column.prop]"
                            :placeholder="'' + column.label" style="width: 8rem"></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addRow('newRow')" type="primary" :loading="loadingbtn"></el-button>
                <el-button @click="resetform"></el-button>
            </span>
        </el-dialog>

        <el-dialog :modal="false" title="" :visible.sync="confirmDeleteDialogVisible" @close="cancelDelete"
            width="5rem">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancelDelete"></el-button>
                <el-button type="danger" @click="deleteRow(confirmedRow)"></el-button>
            </span>
        </el-dialog>

        <el-dialog :modal="false" title="" :visible.sync="imgdialog" @close="imgdialog = false"
            custom-class="imgdialog" top="1rem">
            <div style="width: 100%">
                <img :src="currimgsrc" style="width: 100%" />
            </div>
        </el-dialog>
        <el-pagination v-if="tableData.length > pageSize" class="el-pagin" @size-change="handleSizeChange"
            @current-change="handleCurrentChange" :current-page="currentPage" :page-size="pageSize" :total="total"
            layout="total, prev, pager, next, sizes" :page-sizes="pageSizes" ref="pangination_n" />
        <el-dialog :modal="false" :visible.sync="his_visible" title="" fullscreen custom-class="historydialog">
            <historyDialog ref="history" :current_info="newRow" v-if="his_visible"></historyDialog>
        </el-dialog>
    </div>
</template>

<script>
import Filtercom from "@/views/components/filtercom.vue";
import Uploadocom from "../../components/upload.vue";
import historyDialog from "./historycheckDialog.vue";
import reportapi from "@/api/reportapi";

export default {
    props: ["column", "tabledata", "operate"],

    components: { Uploadocom, Filtercom, historyDialog },
    data() {
        return {
            tableData: [],
            currentPage: 1,
            pageSize: 10,
            pageSizes: [],
            total: 0,
            columns: [],
            addRowDialogVisible: false,
            newRow: {},
            confirmDeleteDialogVisible: false,
            confirmedRow: null,
            value: "",
            loadingbtn: false,
            deviceKind: [
                "",
                "",
                "() ",
            ],
            deviceKind2: [
                "", ""
            ],
            showloading2: "show_loading2",
            imgdialog: false,
            currimgsrc: false,
            checkdata1: ["", "", ""],
            checkedData1: "",
            operatetitle: "",
            rules_data: {
                //   : [
                //     {
                //       required: true,
                //       message: "",
                //       trigger: "change",
                //     },
                //   ],
                //   : [
                //     {
                //       required: true,
                //       message: "",
                //       trigger: "change",
                //     },
                //   ],
            },
            showsearch: false,
            loading: false,
            check_datas: [
                "",
                "",
                "",
                "",
                "",
                "",
                "",
            ],
            his_visible: false,
            showSelect: false,
            showSelectvalue: [],
            pre_currentPage: 1,
            messageinfo: "",

        };
    },
    methods: {
              distinctValues(prop) {
            const values = new Set();
            this.tableData.forEach(item => {
                if (item[prop]) {
                    values.add(item[prop]);
                }
            });
            return Array.from(values).map(value => ({ text: value, value }));
        },
        changeStatus(value) {
            // console.log(value); 
            if (value == '') {
                this.columns = []
                // ...
                // this.rules_data[""] = [
                //     { required: false, message: '', trigger: 'blur' }
                // ]
                this.rules_data[""] = [
                    { required: true, message: '', trigger: 'change' },
                    {
                        validator: (rule, value, callback) => {
                            if (value === "") {
                                // 
                                if (!this.newRow[""] || this.newRow[""].trim() === "") {
                                    callback(new Error(" ''    "));
                                } else {
                                    callback();
                                }
                            } else {
                                callback();
                            }
                        },
                        trigger: 'change'
                    }
                ]
                this.columns = JSON.parse(JSON.stringify(this.column))
                // console.log(this.columns);
                this.rules_data[""] = [
                    { required: true, message: '', trigger: 'blur' }
                ]
            } else {
                this.rules_data[""] = [
                    { required: false, message: '', trigger: 'blur' }
                ]
            }
        },
         showSelectFunc(msg) {
        this.currentPage=1
        this.messageinfo = msg;
        this.$emit("chooseReport", msg);
        this.showSelect = !this.showSelect;
         if(msg==''){
          this.showSelectvalue=[]
         }
        this.$nextTick(() => {
          this.$forceUpdate(); //  Vue 
        });
      },
        confirmSelectFunc(msg) {
            this.$functionConfrim(this.showSelectvalue).then((result) => {
                console.log(result);
                if (result.status == "yes") {
                    this.$emit("reportInfo", {
                        checkevalue: this.showSelectvalue,
                        operate: this.messageinfo,
                    });
                    //   this.$refs.multipleTable.clearSelection();
                }
            });
        },
        handleSelectionChange(val) {
            console.log(val);
            this.showSelectvalue = val;
        },
        viewhistory(row) {
            this.his_visible = true;
            this.newRow = Object.assign({}, row);
        },
        searchFunc() {
            let params = Object.assign({}, this.$refs.filtercom.filterparams);
            this.$emit("searchparams", params);
        },
        //  pageSizes 
        setPageSizes() {
            const totalItems = this.tableData.length;
            this.pageSizes = [];

            //  [10, 20, 30, ...] 
            for (let i = 10; i <= totalItems; i += 10) {
                this.pageSizes.push(i);
            }

            // 10
            if (this.pageSizes.length === 0) {
                this.pageSizes.push(10);
            }
        },
        resetform() {
            this.addRowDialogVisible = false;
            this.operatetitle = "";
            this.initialform();
        },
        initialform() {
            this.$refs.newRow.resetFields();
        },
        cancelImg(column) {
            this.newRow[column] = "";
        },
        imageshowFunc(msg, column) {
            this.newRow[column] = msg;
        },
        showbigimg(src) {
            this.imgdialog = true;
            this.currimgsrc = src;
        },
        // update
        listenUpload() {
            // this.getSAPTable();
        },

        handleCurrentChange(page) {
            // console.log(this.showSelectvalue);
            if (this.showSelectvalue.length) {
                this.$alert('', '', {
                    confirmButtonText: '',
                    type: 'warning'
                }).then(() => {
                    // this.pageSize=0
                    // console.log(this.$refs.pangination_n.internalCurrentPage);
                    this.$nextTick(() => {
                        // this.pageSize=10
                        this.currentPage = this.pre_currentPage
                        this.$refs.pangination_n.internalCurrentPage = this.currentPage
                        this.$forceUpdate()
                    })
                    // console.log(this.currentPage,this.pre_currentPage);
                })
            } else {
                this.currentPage = page;
                this.pre_currentPage = page
            }
        },
        handleSizeChange(size) {
            this.pageSize = size;
        },
        viewDetails(row) {
            console.log(":", row);
        },
        // 
        editRow(row) {
            this.addRowDialogVisible = true;
            this.$nextTick(() => {
                this.newRow = Object.assign({}, row);
                this.rules_data = this.$formatFormRules([], this.newRow, {});
            });
            this.operatetitle = "";
        },
        saveRow(row) {
            row.editing = false;
            console.log(":", row);
        },
        confirmDeleteRow(row) {
            if (this.paginatedData.length == 1) {
                this.$message({
                    type: "warning",
                    message: "",
                    //   duration: 0,
                    showClose: true,
                });
            } else {
                this.confirmDeleteDialogVisible = true;
                this.confirmedRow = row; // 
            }
        },
        cancelDelete() {
            this.confirmDeleteDialogVisible = false;
            this.confirmedRow = null; // 
        },
        deleteRow(row) {
            // const index = this.tableData.indexOf(row);
            console.log(row);
            this.$emit("deleterow", { rowinfo: row, operate: "" });
            // if (index > -1) {
            //   this.tableData.splice(index, 1);
            //   this.total = this.tableData.length; // 
            // }
            // this.cancelDelete(); // 
        },
        rulesCreate() {
            let obj = {};
            this.check_datas.map(
                (item) =>
                (obj[item] = {
                    required: false,
                })
            );
            return obj;
        },
        // 
        openAddRowDialog(row) {
            this.addRowDialogVisible = true;
            this.operatetitle = "";

            this.$nextTick(() => {
                this.formatRow();
                this.newRow = Object.assign({}, row);

                let check = [
                    "",
                    "",
                    "",
                    "",
                    "",
                    "",
                    "",
                    "",
                ];
                for (let i = 0; i < check.length; i++) {
                    this.newRow[check[i]] = "";
                }

                this.newRow[""] = "";
                this.newRow[""] = "";
                //   delete this.newRow['']
                this.rules_data = this.$formatFormRules([], this.newRow, {});
                console.log(this.rules_data);
                // delete this.rules_data['']
            });
        },
        addRow(formname) {
            // console.log(this.newRow)
            // console.log(this.$refs[formname])

            this.$refs[formname].validate((valid) => {
                // console.log(valid)
                if (valid) {
                    this.$emit("addinfo", {
                        rowinfo: this.newRow,
                        operate: this.operatetitle,
                    });
                } else {
                    this.$message({
                        message: "",
                        type: "error",
                    });
                    return false;
                }
            });
        },
        getCellClassName({ row, column, rowIndex, columnIndex }) {
            if (row.age === "" && columnIndex === 2) {
                return "orange-warning"; // 
            } else {
                return "";
            }
        },
        // 
        formatRow() {
            // console.log('aaa',this.columns);
            this.newRow = this.columns.reduce((obj, column) => {
                obj[column.prop] = "";
                return obj;
            }, {});
        },
    },
    computed: {
        paginatedData() {
            this.columns = JSON.parse(JSON.stringify(this.column));
            //    this.columns.splice(6, 7, {
            //     label:"",
            //     prop:"",
            //    })
            let ini_data = JSON.parse(JSON.stringify(this.tabledata))
            this.tableData = ini_data.map((item) => ({
                ...item,
            }));
            this.total = this.tableData.length;
            this.setPageSizes();

            const start = (this.currentPage - 1) * this.pageSize;
            return this.tableData.slice(start, start + this.pageSize);
        },
    },
    mounted() {
        // this.columns = this.columns.filter(item => item.prop != '')

    },
};
</script>

<style lang="less"></style>
