/** */
import request from '@/utils/request'

/** 
 * 
*/
export function readAlarmData(query) {
  return request({
    url: '/mes/watch/alarmwatch/read',
    method: 'get',
    params: query
  })
}

/**
 * 
 * @param {} query 
 * @returns 
 */
export function reconfigAlarmData(query) {
  return request({
    url: '/mes/watch/alarmwatch/reconfig',
    method: 'get',
    params: query
  })
}


