<template>
    <span>{{ weather.temperature }} C {{ weather.weather }} {{ weather.windDirection }}{{ weather.windPower }} {{
        weather.pressure }}</span>
</template>
<script>
import AMapLoader from "@amap/amap-jsapi-loader";

export default {
    name: "map-view",
    mounted() {
        this.initAMap();
        this.timer = setInterval(() => {
            this.getCityWeather()
        }, 300000);
    },

    data() {
        return {
            weather: {},
            timer: null
        }
    },
    methods: {
        initAMap() {
            window._AMapSecurityConfig = {
                securityJsCode: "0880215613b23afb6984e5798737c355",
            };
            AMapLoader.load({
                key: "e2286ba58bd9621be85e31415c7e03d6", // WebKey load 
            })
                .then((AMap) => {
                    this.getCityWeather()
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        // 
        getCityWeather() {
            //
            var that = this
            AMap.plugin('AMap.Weather', function () {
                //
                let weather = new AMap.Weather();
                //
                weather.getLive('', function (err, data) {
                    // console.log(err, data);
                    if (data.info == 'OK') {
                        that.weather = data
                    }
                });

            });
        },
    },
};
</script>
<style scoped></style>