<script>
// import Uploadocom from "./../components/upload.vue";
import Filtercom from "@/views/components/filtercom.vue";
import Uploadocom from "../../components/uploadlist.vue";
import { assignValues_special, isObjectEmpty } from "@/api/globalparams";

export default {
  props: ["column", "tabledata", "operate"],

  components: { Uploadocom, Filtercom },
  data() {
    return {
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      pageSizes: [],
      total: 0,
      columns: [],
      addRowDialogVisible: false,
      newRow: {},
      confirmDeleteDialogVisible: false,
      confirmedRow: null,
      value: "",
      loadingbtn: false,
      deviceKind: ["", ""],
      showloading2: "show_loading2",
      imgdialog: false,
      currimgsrc: false,
      checkdata1: ["", "", ""],
      checkedData1: "",
      operatetitle: "",
      rulesNumber: {
        number: [
          { required: true, message: "", trigger: "blur" },
          { validator: this.validateNumber, trigger: "blur" },
        ],
      },
      heightvalue: "",
      rules_data: {},
      showsearch: false,
      change_base: false,
      loading: false,
      check_datas: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      write_flag: false,
    };
  },
  methods: {
    searchFunc() {
      let params = Object.assign({}, this.$refs.filtercom.filterparams);
      this.$emit("searchparams", params);
    },
    //  pageSizes 
    setPageSizes() {
      const totalItems = this.tableData.length;
      this.pageSizes = [];

      //  [10, 20, 30, ...] 
      for (let i = 10; i <= totalItems; i += 10) {
        this.pageSizes.push(i);
      }

      // 10
      if (this.pageSizes.length === 0) {
        this.pageSizes.push(10);
      }
    },
    showcellimg(scope, name) {
      //  row.imgsource=row[scope.$index]
      this.$set(this.paginatedData[scope.$index], "imgsource", scope.row[name]);
      this.$set(scope.row, "imgsource", scope.row[name]);
      console.log(this.paginatedData[scope.$index]);
      this.$forceUpdate();
    },
    validateNumber(rule, value, callback) {
      const number = parseInt(value, 10);
      if (!number || number < 1 || number > 12) {
        callback(new Error(" 1  12 "));
      } else {
        callback();
      }
    },
    resetform() {
      this.addRowDialogVisible = false;
      this.operatetitle = "";
      this.initialform();
    },
    initialform() {
      this.$refs.newRow.resetFields();
    },
    cancelImg(column, img) {
      // if(this.d)
      this.newRow[column] = this.newRow[column].filter((item) => item != img);
    },
    imageshowFunc(msg, column) {
      this.newRow[column] = msg;
      console.log("", this.newRow[column]);
    },
    showbigimg(src) {
      this.imgdialog = true;
      this.currimgsrc = src;
    },
    // update
    listenUpload() {
      // this.getSAPTable();
    },

    handleCurrentChange(page) {
      // console.log(this.showSelectvalue);
      //   if (this.showSelectvalue.length) {
      //     this.$alert("", "", {
      //       confirmButtonText: "",
      //       type: "warning",
      //     }).then(() => {
      //       // this.pageSize=0
      //       // console.log(this.$refs.pangination_n.internalCurrentPage);
      //       this.$nextTick(() => {
      //         // this.pageSize=10
      //         this.currentPage = this.pre_currentPage;
      //         this.$refs.pangination_n.internalCurrentPage = this.currentPage;
      //         this.$forceUpdate();
      //       });
      //       // console.log(this.currentPage,this.pre_currentPage);
      //     });
      //   } else {
      this.currentPage = page;
      this.pre_currentPage = page;
      //   }
    },
    handleSizeChange(size) {
      this.pageSize = size;
    },
    viewDetails(row) {
      console.log(":", row);
    },
    saveRow(row) {
      row.editing = false;
      console.log(":", row);
    },
    confirmDeleteRow(row) {
      if (this.paginatedData.length == 1) {
        this.$message({
          type: "warning",
          message: "",
          //   duration: 0,
          showClose: true,
        });
      } else {
        this.confirmDeleteDialogVisible = true;
        this.confirmedRow = row; // 
      }
    },
    cancelDelete() {
      this.confirmDeleteDialogVisible = false;
      this.confirmedRow = null; // 
    },
    deleteRow(row) {
      // const index = this.tableData.indexOf(row);
      console.log(row);
      this.$emit("deleterow", { rowinfo: row, operate: "" });
      // if (index > -1) {
      //   this.tableData.splice(index, 1);
      //   this.total = this.tableData.length; // 
      // }
      // this.cancelDelete(); // 
    },
    openAddRowDialog() {
      this.addRowDialogVisible = true;
      this.operatetitle = "";
      this.$nextTick(() => {
        this.formatRow();
        this.newRow[""] = "";
      });
    },
    // 
    editRow(row) {
      this.addRowDialogVisible = true;
      this.write_flag = false;
      console.log(row);
      //   this.newRow = JSON.parse(JSON.stringify(row));
      // assignValues_special
      let flag_p1 = assignValues_special(undefined, row);
      let infoparam = JSON.parse(JSON.stringify(flag_p1[1]));
      delete infoparam.ID;
      this.write_flag = isObjectEmpty(infoparam);
      this.$nextTick(() => {
        this.newRow = JSON.parse(JSON.stringify(row));
        for (let i in row) {
          if (i.indexOf("") >= 0) {
            this.newRow[i] = row[i] ? row[i].split("|") : [];
          }
        }
        this.operatetitle = "";
      });
      //   console.log(this.newRow);
    },
    addRow(formname) {
      // console.log(this.newRow)
      // console.log(this.$refs[formname])

      this.$refs[formname].validate((valid) => {
        // console.log(valid)
        if (valid) {
          this.$emit("addinfo", {
            rowinfo: this.newRow,
            operate: this.operatetitle,
            write_flag: this.write_flag,
          });
        } else {
          this.$message({
            message: "",
            type: "error",
          });
          return false;
        }
      });
    },
    // 

    formatRow() {
      // 
      this.newRow = this.columns.reduce((obj, column) => {
        obj[column.prop] = column.prop.indexOf("") >= 0 ? [] : "";
        return obj;
      }, {});
    },
  },
  computed: {
    paginatedData() {
      this.columns = this.column;
      this.tableData = this.tabledata.map((item) => ({
        ...item,
      }));
      this.total = this.tableData.length;
      this.setPageSizes();

      const start = (this.currentPage - 1) * this.pageSize;
      setTimeout(() => {
        this.heightvalue = "calc(100vh - 3.2rem)";
      }, 2000);
      return this.tableData.slice(start, start + this.pageSize);
    },
  },
  mounted() {
    this.rules_data = this.$formatFormRules(
      [
        "",
        "",
        "",
        "m3",
        "t",
        "",
        "m3",
        "t",
      ],
      this.newRow,
      {
        "": {
          required: false,
        },
        "": {
          required: false,
        },
        "": {
          required: false,
        },
        "m3": {
          required: false,
        },
        "t": {
          required: false,
        },
        "": {
          required: false,
        },
        "m3": {
          required: false,
        },
        "t": {
          required: false,
        },
      }
    );
  },
};
