<template>
    <div style="padding: 20px" class="base_list highlight">
        <edit-table :column="columns" :tabledata="tableData" :loading="loading"></edit-table>
        <div class="echarts-foot"></div>
    </div>
</template>

<script>
import EditTable from "../home/components/editTable.vue";
import aes from '../../api/aes'
import reportapi from "@/api/reportapi";
import moment from 'moment'
export default {
    components: { EditTable },
    data() {
        return {
            tableData: [],
            columns: [
                // { prop: "id", label: "", sortable: true },
                { prop: "warnOrg", label: "", },
                { prop: "warnRank", label: "", width: "80rem" },
                { prop: "warnStatus", label: "", width: "70rem" },
                { prop: "riskIndex", label: "", width: "90rem" },
                { prop: "startTime", label: "", width: "180rem" },
                { prop: "endTime", label: "" },
                { prop: "message", label: "", width: "290rem" },
            ],
            loading: true,
            timer: null
        };
    },
    destroyed() {
        this.disableTimer();
    },
    mounted() {
        this.exampleUsage();
        this.timer = setInterval(this.exampleUsage, 60000);

    },
    methods: {
        disableTimer() {
            // console.log("hhh", this.timer);
            if (this.timer != null) {
                clearInterval(this.timer);
                this.timer = null;
            }
        },
        // 
        async exampleUsage() {
            this.loading = true
            let datevalue = moment(new Date().getTime()).format('yyyy-MM-DD HH:mm:ss')
            let res = await reportapi.zfwarningInfo({
                dataId: 'GT3210840001',
                // startTime:datevalue
                startTime: '2024-10-01 00:00:00'
            })
            if (res.code == 2000) {
                const encryptedData = res.data; // 
                const decryptedData = aes.decryptWithAES(encryptedData);
                console.log(':', decryptedData);
                this.tableData = decryptedData.records
            } else {
                this.$message.error("")
            }
            this.loading = false

        },
    },
};
</script>

