            option && myChart5.setOption(option);
            window.addEventListener(
                "resize",
                () => {
                    myChart5.resize();
                },
                false
            );
        },
        getBarData() {
            let myChart6 = this.$echarts.init(document.getElementById("bar"));
            var option = {
                color: ["#20E2E9", "#00d887"],
                dataZoom: [
                    {
                        type: "inside",
