                    },
                    // boundaryGap:'0px'
                },
                yAxis: {
                    axisLine: {
                        show: false,
                    },
                    axisTick: {
                        show: false,
                    },
                    axisLabel: {
                        show: false,
                    },
                    splitLine: {
                        show: false,
                    },
                },
                series: [
                    {
                        name: "",
                        type: "bar",
                        data: this.menList,
                        smooth: true,
                        symbol: "circle",
                        barWidth: 15, //
                        barGap: "100%", //
                        lineStyle: {
                            normal: {
                                color: "#20E2E9",
                                width: "2px",
