                        data: this.womenList,
                        smooth: true,
                        symbol: "circle",
                        barWidth: 15, //
                        barGap: "80%", //
                        symbolSize: "5px",
                        showSymbol: false,
                        lineStyle: {
                            normal: {
                                color: "#00d887",
                                width: "2px",
