<template>
  <div style="display: inline-block">
    <el-upload
      :action="uploadUrl"
      name="file"
      :on-success="handleSuccess"
      :on-error="handleError"
      :before-upload="beforeUpload"
      :http-request="customRequest"
      :show-file-list="false"
      accept=".png,.jpg"
    >
      <i class="el-icon-upload2 download"></i>
      <img
        src="../../assets/images/loading.gif"
        class="loadgif"
        v-if="loading"
        style="width: 0.3rem"
      />
      <div class="el-upload__tip" slot="tip">
         jpg/png/jpeg  500kb
      </div>
    </el-upload>
  </div>
</template>

<script>
import axios from "axios";
import reportapi from "@/api/reportapi";
export default {
  props: ["referType"],
  data() {
    return {
      uploadUrl: "/api/function/upload", // 

      // uploadUrl: "/api/function/upload", // 
      loading: false, // 
      fileId: "", //  ID
      action_flag: false,
    };
  },
  methods: {
    // 
    async beforeUpload(file) {
      const allowedExtensions = ["png", "jpg"];
      const fileExtension = file.name.split(".").pop().toLowerCase();

      // 
      if (!allowedExtensions.includes(fileExtension)) {
        this.$message.warning(" jpg/png ");
        return false;
      }

      //  500kb
      const isLt500KB = file.size / 1024 <= 500;
      if (!isLt500KB) {
        this.$message.warning(" 500kb");
        return false;
      }

      // 
      try {
        await this.$confirm("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        });

        //  ID
        const response = await reportapi.getReportId();
        if (response.data.code === 200) {
          this.fileId = response.data.data; //  ID  `data` 
          this.loading = true; // 
          this.action_flag = true;
          return true;
        } else {
          this.$message.error(" ID " + response.data.message);
          this.action_flag = false;
          return false;
        }
      } catch (error) {
        this.$message.info("");
        console.log("");
        this.action_flag = false;

        return false;
      }
    },

    // 
    customRequest({ file }) {
      if (this.action_flag) {
        const formData = new FormData();

        //  ID
        const fileExtension = file.name.split(".").pop();
        //   console.log(f);
        const renamedFile = new File(
          [file],
          `${this.fileId}.${fileExtension}`,
          {
            type: file.type,
          }
        );

        // 
        formData.append("file", renamedFile);
        formData.append("relativeId", this.fileId);
        formData.append("referType", this.referType);
        //   formData.append("fileId", this.fileId); // 
        console.log(formData);
        //  Axios 
        axios
          .post(this.uploadUrl, formData, {
            headers: { "Content-Type": "multipart/form-data" },
          })
          .then((response) => {
            console.log(response.data);
            if (response.data.code == 200) {
              this.handleSuccess(response.data);
              this.$emit("imageshow", response.data.url);
            } else {
              this.handleError(response.data.message);
            }
          })
          .catch((error) => {
            this.handleError(error);
          });
      }
    },

    // 
    handleSuccess(response) {
      this.loading = false;
      this.$message.success("");
      console.log("", response);
    },

    // 
    handleError(error) {
      this.loading = false;
      this.$message.error("");
      console.error("", error);
    },
  },
};
</script>
