<template>
  <div>
    <hkVideo
      v-if="protocol == 'hkvideo'"
      ref="video1"
      :data="hkData"
      :newId="divId"
      :visible="videoVisible"
      :loading_time="loading_time"
    />
    <!-- <rtspVideo
        v-if="protocol == 'rtsp'"
        width="100%"
        class="video-img1"
        ref="video1"
        :props-data="rtspData"
        :newId="divId"
      /> -->
    <!-- <flvVideo :flv="flvList.camera1"></flvVideo> -->
  </div>
</template>

<script>
import hkVideo from "./hkvideo.vue";
//import rtspVideo from "./rtspVideo.vue";
//import flvVideo from "flvideo.vue";
export default {
  name: "VideoBox",
  props: {
    data: {
      //
      type: Object || Array,
      require: true,
    },
    muted: {
      type: Boolean,
      require: false,
    },
    newId: {
      //id
      type: String,
      require: true,
    },
  },
  components: {
    hkVideo,
    // rtspVideo,
    //flvVideo
  },
  data() {
    return {
      divId: "divABC",
      //,
      protocol: "hkvideo", //
      hkData: {}, //
      rtspData: {}, //rtsp,
      videoVisible: true,
      loading_time: "",
    };
  },
  watch: {
    data: {
      deep: true,
      immediate: true,
      handler(newVal, oldVal) {
        //
        // console.log("VideoBox:", newVal);
        if (newVal) {
          this.protocol = newVal.protocol; //
            this.loading_time = newVal.loadingtime; //
        //   console.log('ooooopooo', this.loading_time );
          if (newVal.hkData) this.hkData = newVal.hkData;
          else this.hkData = undefined;
          if (newVal.rtspData) this.rtspData = newVal.rtspData;
          else this.rtspData = undefined;
        }
      },
    },
    newId: {
      deep: true,
      immediate: true,
      handler(newVal, oldVal) {
        this.divId = "divABC" + newVal; //id
      },
    },
  },
  mounted() {
    //this.hkData = JSON.parse(JSON.stringify(this.hkData_info));
    // this.makeHkData(this.hkData_info);
  },
  created() {},
  destroyed() {
    this.closeVideo();
  },
  methods: {
    //
    closeVideo() {
      //
      var that = this;
      that.$nextTick(() => {
        if (that.$refs) {
          console.log("refs:", that.$refs);
          if (that.$refs.video1) {
            that.$refs.video1.closeVideo();
          }
        }
      });
    },
    fitVideo() {
      //
      var that = this;
      this.$nextTick(() => {
        if (that.$refs) {
          console.log("refs:", that.$refs);
          if (that.$refs.video1) {
            that.$refs.video1.fitVideo(); //
          }
        }
      });
    },
    showVideo(flag) {
      //
      var that = this;
      that.videoVisible = flag;
    },
    closedSound() {
      this.$nextTick(() => {
        this.$refs.video1.volume = 0;
        this.$refs.video2.volume = 0;
      });
    },
  },
};
</script>

<style lang="less"></style>
