  <script>
  import Filtercom from "@/views/components/filtercom.vue";
  import Uploadocom from "../../components/upload.vue";
  export default {
    props: ["column", "tabledata", "operate"],
  
    components: { Uploadocom, Filtercom },
    data() {
      return {
        tableData: [],
        currentPage: 1,
        pageSize: 10,
        pageSizes: [],
        total: 0,
        columns: [],
        addRowDialogVisible: false,
        newRow: {},
        confirmDeleteDialogVisible: false,
        confirmedRow: null,
        value: "",
        loadingbtn: false,
        deviceKind: [{
          label:"A",
          value:'1'
        },{
          label:"B",
          value:'2'
        }],
        showloading2: "show_loading2",
        imgdialog: false,
        currimgsrc: false,
        checkdata1: ["", "", ""],
        checkedData1: "",
        operatetitle: "",
        rulesNumber: {
          number: [
            { required: true, message: "", trigger: "blur" },
            { validator: this.validateNumber, trigger: "blur" },
          ],
        },
        rules_data: {},
        showsearch: false,
        loading: false,
        check_datas: [
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
        ],
        showSelect: false,
        showSelectvalue: [],
        pre_currentPage: 1,
        messageinfo: "",
        tabledialog: false,
        position_type: [
          { label: "", value: "1001" },
          { label: "", value: "1002" },
          { label: "", value: "1003" },
          { label: "", value: "1004" },
          { label: "", value: "1005" },
          { label: "", value: "1006" },
          { label: "", value: "1007" },
          {
            label:
              "",
            value: "2001",
          },
          { label: "", value: "2002" },
          { label: "", value: "2003" },
          { label: "", value: "2004" },
          {
            label: "",
            value: "2005",
          },
          { label: "", value: "2006" },
          { label: "", value: "2007" },
          { label: "", value: "2008" },
          { label: "", value: "2009" },
          { label: "", value: "2010" },
          { label: "", value: "2011" },
          { label: "", value: "2012" },
          { label: " TRT ", value: "2013" },
          { label: "", value: "2014" },
          { label: "", value: "2015" },
          { label: "", value: "2016" },
          { label: "", value: "2017" },
          { label: "", value: "2018" },
          { label: "", value: "2019" },
          { label: "", value: "2020" },
          { label: "", value: "2021" },
          { label: "", value: "2022" },
          { label: "", value: "2023" },
          { label: "", value: "2024" },
          { label: "", value: "2025" },
          { label: "", value: "2026" },
          { label: "", value: "2027" },
          { label: "", value: "" },
        ],
      };
    },
  
    methods: {
     formatShowData(row, name) {
      //   console.log(row, name);
      //   console.log(this.gtType);
      if (this[name].find((info) => info.value == row)) {
        return this[name].find((info) => info.value == row).label;
      }
    },
      showSelectFunc(msg) {
        this.currentPage=1
        this.messageinfo = msg;
        this.$emit("chooseReport", msg);
        this.showSelect = !this.showSelect;
         if(msg==''){
          this.showSelectvalue=[]
         }
        this.$nextTick(() => {
          this.$forceUpdate(); //  Vue 
        });
      },
      confirmSelectFunc(msg) {
        this.$functionConfrim(this.showSelectvalue).then((result) => {
          console.log(result);
          if (result.status == "yes") {
            this.$emit("reportInfo", {
              checkevalue: JSON.parse(JSON.stringify(this.showSelectvalue)),
              operate: this.messageinfo,
            });
            //   this.$refs.multipleTable.clearSelection();
          }
        });
      },
      handleSelectionChange(val) {
        console.log(val);
        this.showSelectvalue = val;
      },
      searchFunc() {
        let params = Object.assign({}, this.$refs.filtercom.filterparams);
        this.$emit("searchparams", params);
      },
      //  pageSizes 
      setPageSizes() {
        const totalItems = this.tableData.length;
        this.pageSizes = [];
  
        //  [10, 20, 30, ...] 
        for (let i = 10; i <= totalItems; i += 10) {
          this.pageSizes.push(i);
        }
  
        // 10
        if (this.pageSizes.length === 0) {
          this.pageSizes.push(10);
        }
      },
      validateNumber(rule, value, callback) {
        const number = parseInt(value, 10);
        if (!number || number < 1 || number > 12) {
          callback(new Error(" 1  12 "));
        } else {
          callback();
        }
      },
      resetform() {
        this.addRowDialogVisible = false;
        this.operatetitle = "";
        this.initialform();
      },
      initialform() {
        this.$refs.newRow.resetFields();
      },
      cancelImg(column) {
        this.newRow[column] = "";
      },
      imageshowFunc(msg, column) {
        this.newRow[column] = msg;
      },
      showbigimg(src) {
        this.imgdialog = true;
        this.currimgsrc = src;
      },
      // update
      listenUpload() {
        // this.getSAPTable();
      },
  
      handleCurrentChange(page) {
        // console.log(this.showSelectvalue);
        if (this.showSelectvalue.length) {
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning",
          }).then(() => {
            // this.pageSize=0
            // console.log(this.$refs.pangination_n.internalCurrentPage);
            this.$nextTick(() => {
              // this.pageSize=10
              this.currentPage = this.pre_currentPage;
              this.$refs.pangination_n.internalCurrentPage = this.currentPage;
              this.$forceUpdate();
            });
            // console.log(this.currentPage,this.pre_currentPage);
          });
        } else {
          this.currentPage = page;
          this.pre_currentPage = page;
        }
      },
      handleSizeChange(size) {
        this.pageSize = size;
      },
      viewDetails(row) {
        console.log(":", row);
      },
      // 
      editRow(row) {
        this.addRowDialogVisible = true;
        this.$nextTick(() => {
          this.newRow = Object.assign({}, row);
          this.rules_data = this.$formatFormRules(["PPM"], this.newRow,{});
        });
        this.operatetitle = "";
      },
      saveRow(row) {
        row.editing = false;
        console.log(":", row);
      },
      confirmDeleteRow(row) {
        if (this.paginatedData.length == 1) {
          this.$message({
            type: "warning",
            message: "",
            //   duration: 0,
            showClose: true,
          });
        } else {
          this.confirmDeleteDialogVisible = true;
          this.confirmedRow = row; // 
        }
      },
      cancelDelete() {
        this.confirmDeleteDialogVisible = false;
        this.confirmedRow = null; // 
      },
      deleteRow(row) {
        // const index = this.tableData.indexOf(row);
        console.log(row);
        this.$emit("deleterow", { rowinfo: row, operate: "" });
        // if (index > -1) {
        //   this.tableData.splice(index, 1);
        //   this.total = this.tableData.length; // 
        // }
        // this.cancelDelete(); // 
      },
      openAddRowDialog() {
        this.addRowDialogVisible = true;
        this.operatetitle = "";
        this.newRow[""] = "";
        this.$nextTick(() => {
          this.formatRow();
          // console.log(this.newRow);
          this.newRow[""] = "";
          this.rules_data = this.$formatFormRules(["PPM"], this.newRow,{});
          // this.rules_data = {
          //   PPM: [
          //     {
          //       pattern: /^[1-9]\d*(\.\d+)?$/,
          //       message: "",
          //       trigger: "change",
          //     },
          //   ],
          //   : [
          //     {
          //       required: true,
          //       message: "",
          //       trigger: "change",
          //     },
          //   ],
          // };
          // console.log(this.rules_data);
        });
      },
      addRow(formname) {
        // console.log(this.newRow)
        // console.log(this.$refs[formname])
  
        this.$refs[formname].validate((valid) => {
          // console.log(valid)
          if (valid) {
            this.$emit("addinfo", {
              rowinfo: this.newRow,
              operate: this.operatetitle,
            });
          } else {
            this.$message({
              message: "",
              type: "error",
            });
            return false;
          }
        });
      },
      getCellClassName({ row, column, rowIndex, columnIndex }) {
        if (row.age === "" && columnIndex === 2) {
          return "orange-warning"; // 
        } else {
          return "";
        }
      },
      // 
  
      formatRow() {
        this.newRow = this.columns.reduce((obj, column) => {
          obj[column.prop] = "";
          return obj;
        }, {});
      },
    },
    computed: {
      paginatedData() {
        this.columns = this.column;
        this.tableData = this.tabledata.map((item) => ({
          ...item,
        }));
        this.total = this.tableData.length;
        this.setPageSizes();
        this.formatRow();
        //   this.rules_data = {
        //     PPM: [
        //       {
        //         pattern: /^[1-9]\d*(\.\d+)?$/,
        //         message: "",
        //         trigger: "change",
        //       },
        //     ],
        //     : [
        //       {
        //         required: true,
        //         message: "",
        //         trigger: "change",
        //       },
        //     ],
        //   };
        const start = (this.currentPage - 1) * this.pageSize;
        return this.tableData.slice(start, start + this.pageSize);
      },
    },
    mounted() {},
  };
  </script>
  
  <style lang="less"></style>
  