      getChannelInfo() {
        this.channelsList = [];
  
        if (this.szDeviceIdentify === "") {
          return false;
        }
  
        // 
        WebVideoCtrl.I_GetDigitalChannelInfo(this.szDeviceIdentify, {
          async: false,
