<template>
  <div>
    <div class="machineTable">
      <img
        src="../../../assets/images/loading.gif"
        class="loadgif"
        id="show_loading2"
      />

      <p></p>
    </div>
    <el-row type="flex">
      <el-button
        type="primary"
        @click="openAddRowDialog"
        icon="el-icon-plus"
        class="plusicon"
        v-if="!showSelect"
        ></el-button
      >
      <!-- <el-button
        type="text"
        @click="showSelectFunc"
        class="plusicon" 
        v-if="!showSelect"
      >
        <span></span>
      </el-button> -->
      <!-- <div v-if="showSelect" style="width: fit-content">
        <el-button
          type="text"
          @click="confirmSelectFunc"
          class="plusicon"
          v-if="showSelect"
          style="margin-right: 0.1rem"
        >
          <span></span>
        </el-button>
        <el-button
          type="text"
          @click="showSelectFunc"
          class="plusicon"
          v-if="showSelect"
          style="margin-right: 0.1rem; color: grey"
        >
          <span></span>
        </el-button>
      </div> -->
    </el-row>
    <div class="table-container">
      <el-table
        bordered
        row-class-name="rowstyle"
        :data="paginatedData"
        style="width: 100%; background: transparent"
        :cell-class-name="getCellClassName"
        height="calc(100vh - 3.2rem)"
        @selection-change="handleSelectionChange"
        :key="showSelect"
      >
        <el-table-column label="" v-if="!showSelect" minWidth="110rem">
          <template #default="scope">
            <el-button
              v-if="operate[1] == 'delete'"
              @click="confirmDeleteRow(scope.row)"
              type="text"
              size="small"
              style="color: red; font-size: 0.175rem"
            >
              
            </el-button>
            <el-button
              @click="editRow(scope.row)"
              type="text"
              size="small"
              style="font-size: 0.175rem"
            >
              
            </el-button>
          </template>
        </el-table-column>
        <el-table-column type="selection" width="55" v-if="showSelect">
        </el-table-column>
        <!-- <el-table-column label="">
          <template #default="scope">
            {{ scope.row["addFlag"] == 1 ? "" : "" }}
          </template>
        </el-table-column> -->
        <el-table-column
          fit
          v-for="(column, index) in columns"
          :key="index"
          :prop="column.prop"
          :label="column.label"
          :sortable="column.sortable"
          v-if="column.label != 'ID'"
        >
          <template #default="scope">
            <div v-if="!scope.row.editing">
              <div
                v-if="
                  column.prop == '' ||
                  column.prop == '/'
                "
                @click="showbigimg(scope.row[column.prop])"
              >
                <img
                  :src="scope.row[column.prop]"
                  style="width: 0.5rem"
                  v-if="scope.row[column.prop]"
                />
                <span v-else></span>
              </div>
              <div v-if="column.prop == ''">
                <img
                  :src="
                    scope.row[column.prop] != 'grey'
                      ? require(`../../../assets/images/${
                          scope.row[column.prop]
                        }light.png`)
                      : require(`../../../assets/images/greenlight.png`)
                  "
                  class="lightimage"
                  :style="
                    scope.row[column.prop] != 'grey'
                      ? ''
                      : 'filter: grayscale(100%)'
                  "
                />
              </div>
              <span
                v-else-if="
                  column.prop != '' &&
                  column.prop != '' &&
                  column.prop != '/'
                "
              >
                <span v-if="column.prop.indexOf('') >= 0">{{
                  scope.row[column.prop].replace("T00:00:00", "")
                }}</span>
                <span v-else>
                  {{ scope.row[column.prop] }}
                </span>
              </span>
            </div>

            <!-- <div v-else>
              <el-input
                v-model="scope.row[column.prop]"
                
              ></el-input>
            </div> -->
          </template>
        </el-table-column>
      </el-table>
      <el-button
        type="primary"
        @click="
          $downloadExcel({ tableData: tabledata, title: '' })
        "
        class="plusicon"
        size="small"
        ></el-button
      >
    </div>

    <el-dialog
      :title="operatetitle"
      :visible.sync="addRowDialogVisible"
      :modal="false"
      top="0.8rem"
      v-if="operate[2] == 'add'"
    >
      <div class="dialog-content">
        <el-form
          :model="newRow"
          label-position="top"
          ref="newRow"
          :rules="rules_data"
        >
          <el-form-item
            v-for="column in columns"
            :key="column.prop"
            :label="column.label"
            :prop="column.prop"
            v-if="column.label != 'ID' && column.prop != 'ID'"
          >
            <div v-if="column.prop == ''">
              <el-radio-group v-model="newRow[column.prop]">
                <el-radio
                  v-for="city in checkdata1"
                  :label="city"
                  :key="city"
                  >{{ city }}</el-radio
                >
              </el-radio-group>
            </div>
            <el-select
              v-if="column.prop == ''"
              v-model="newRow[column.prop]"
              style="width: 8rem"
            >
              <el-option
                v-for="item in deviceKind"
                :label="item"
                :value="item"
                :key="item"
              ></el-option>
            </el-select>

            <el-date-picker
              v-if="column.prop.indexOf('') >= 0"
              v-model="newRow[column.prop]"
              type="year"
              placeholder=""
              value-format="yyyy"
            >
            </el-date-picker>
            <div v-if="column.prop == ''">
              <el-radio-group v-model="newRow[column.prop]">
                <el-radio
                  v-for="city in checkdata1"
                  :label="city"
                  :key="city"
                  >{{ city }}</el-radio
                >
              </el-radio-group>
            </div>
            <el-switch
              v-if="column.prop == ''"
              v-model="newRow[column.prop]"
              active-text=""
              inactive-text=""
              :active-value="1"
              :inactive-value="0"
            >
            </el-switch>
            <el-input
              v-else-if="
                column.prop.indexOf('') < 0 &&
                column.prop != '' &&
                column.prop != ''
              "
              v-model="newRow[column.prop]"
              :placeholder="'' + column.label"
              style="width: 8rem"
            ></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button
          @click="addRow('newRow')"
          type="primary"
          :loading="loadingbtn"
          ></el-button
        >
        <el-button @click="resetform"></el-button>
      </span>
    </el-dialog>

    <el-dialog
      :modal="false"
      title=""
      :visible.sync="confirmDeleteDialogVisible"
      @close="cancelDelete"
      width="5rem"
    >
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelDelete"></el-button>
        <el-button type="danger" @click="deleteRow(confirmedRow)"
          ></el-button
        >
      </span>
    </el-dialog>

    <el-dialog
      :modal="false"
      title=""
      :visible.sync="imgdialog"
      @close="imgdialog = false"
      custom-class="imgdialog"
      top="1rem"
    >
      <div style="width: 100%">
        <img :src="currimgsrc" style="width: 100%" />
      </div>
    </el-dialog>

    <el-pagination
      v-if="tableData.length > pageSize"
      class="el-pagin"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="total"
      layout="total, prev, pager, next, sizes"
      :page-sizes="pageSizes"
      ref="pangination_n"
    />
  </div>
</template>

<script>
import Uploadocom from "../../components/upload.vue";
export default {
  props: ["column", "tabledata", "operate"],

  components: { Uploadocom },
  data() {
    return {
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      pageSizes: [],
      total: 0,
      columns: [],
      addRowDialogVisible: false,
      newRow: {},
      confirmDeleteDialogVisible: false,
      confirmedRow: null,
      value: "",
      loadingbtn: false,
      deviceKind: [
        "",
        "",
        "AOD",
        "LF",
        "VOD",
        "CAS",
        "IR",
        "RH",
        "",
        "Consteel",
        "",
      ],
      showloading2: "show_loading2",
      imgdialog: false,
      currimgsrc: false,
      checkedData1: "",
      operatetitle: "",
      rules_data: {},
      checkdata1: [
        "TCP/IP",
        "OPC",
        "Modbus",
      ],
      showSelect: false,
      showSelectvalue: [],
      pre_currentPage: 1,
    };
  },
  methods: {
    confirmSelectFunc() {
      this.$functionConfrim(this.showSelectvalue).then((result) => {
        console.log(result);
        if (result == "yes") {
          this.$refs.multipleTable.clearSelection();
        }
      });
    },
    showSelectFunc() {
      this.showSelect = !this.showSelect;
      this.$nextTick(() => {
        this.$forceUpdate(); //  Vue 
      });
    },
    handleSelectionChange(val) {
      console.log(val);
      this.showSelectvalue = val;
    },
    //  pageSizes 
    setPageSizes() {
      const totalItems = this.tableData.length;
      this.pageSizes = [];

      //  [10, 20, 30, ...] 
      for (let i = 10; i <= totalItems; i += 10) {
        this.pageSizes.push(i);
      }

      // 10
      if (this.pageSizes.length === 0) {
        this.pageSizes.push(10);
      }
    },
    resetform() {
      this.addRowDialogVisible = false;
      this.operatetitle = "";
      this.initialform();
    },
    initialform() {
      this.$refs.newRow.resetFields();
    },
    cancelImg(column) {
      this.newRow[column] = "";
    },
    imageshowFunc(msg, column) {
      this.newRow[column] = msg;
    },
    showbigimg(src) {
      this.imgdialog = true;
      this.currimgsrc = src;
    },
    // update
    listenUpload() {
      // this.getSAPTable();
    },
    handleCurrentChange(page) {
      // console.log(this.showSelectvalue);
      if (this.showSelectvalue.length) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning",
        }).then(() => {
          // this.pageSize=0
          // console.log(this.$refs.pangination_n.internalCurrentPage);
          this.$nextTick(() => {
            // this.pageSize=10
            this.currentPage = this.pre_currentPage;
            this.$refs.pangination_n.internalCurrentPage = this.currentPage;
            this.$forceUpdate();
          });
          // console.log(this.currentPage,this.pre_currentPage);
        });
      } else {
        this.currentPage = page;
        this.pre_currentPage = page;
      }
    },
    handleSizeChange(size) {
      this.pageSize = size;
    },
    viewDetails(row) {
      console.log(":", row);
    },
    // 
    editRow(row) {
      this.addRowDialogVisible = true;
      this.newRow = JSON.parse(JSON.stringify(row));
      console.log(row);
      console.log(row[""] == "" ? 1 : 0);
      this.newRow[""] = row[""] == "" ? 1 : 0;
      console.log(this.newRow);

      this.operatetitle = "";
      // row.editing = true;
    },
    saveRow(row) {
      row.editing = false;
      console.log(":", row);
    },
    confirmDeleteRow(row) {
      if (this.paginatedData.length == 1) {
        this.$message({
          type: "warning",
          message: "",
          //   duration: 0,
          showClose: true,
        });
      } else {
        this.confirmDeleteDialogVisible = true;
        this.confirmedRow = row; // 
      }
    },
    cancelDelete() {
      this.confirmDeleteDialogVisible = false;
      this.confirmedRow = null; // 
    },
    deleteRow(row) {
      // const index = this.tableData.indexOf(row);
      console.log(row);
      this.$emit("deleterow", { rowinfo: row, operate: "" });
      // if (index > -1) {
      //   this.tableData.splice(index, 1);
      //   this.total = this.tableData.length; // 
      // }
      // this.cancelDelete(); // 
    },
    openAddRowDialog() {
      this.addRowDialogVisible = true;
      this.$nextTick(() => {
        this.formatRow();
        this.initialform();
      });
    },
    addRow(formname) {
      console.log(this.newRow);
      // console.log(this.$refs[formname])
      this.$refs[formname].validate((valid) => {
        // console.log(valid)
        if (valid) {
          this.$emit("addinfo", {
            rowinfo: this.newRow,
            operate: this.operatetitle,
          });
        } else {
          this.$message({
            message: "",
            type: "error",
          });
          return false;
        }
      });
    },
    getCellClassName({ row, column, rowIndex, columnIndex }) {
      if (row.age === "" && columnIndex === 2) {
        return "orange-warning"; // 
      } else {
        return "";
      }
    },
    // 

    formatRow() {
      this.newRow = this.columns.reduce((obj, column) => {
        obj[column.prop] = "";
        return obj;
      }, {});
      this.newRow[""] = "0";
    },
  },
  computed: {
    paginatedData() {
      const start = (this.currentPage - 1) * this.pageSize;
      return this.tableData.slice(start, start + this.pageSize);
    },
  },
  mounted() {
    this.columns = this.column;
    this.tableData = this.tabledata.map((item) => ({
      ...item,
    }));
    this.total = this.tableData.length;
    this.setPageSizes();
    this.formatRow();

    this.rules_data = this.$formatFormRules([], this.newRow, {
      : {
        required: false,
      },
    });
    // console.log(this.rules_data);
  },
};
</script>

