<template>
    <div style="width: 100%; height: 100%">
        <div class="loadgif" v-if="loading" style="top: -10%">
            <img src="../../../assets/images/loading.gif" id="show_loading" /><span style="color: white"></span>
        </div>

        <div :id="myNewId" class="plugin" style="height: 100%; width: 100%" v-loading="loading"></div>
        <el-form :model="queryParams" ref="queryForm" size="small" :inline="true" v-show="showSearch"
            label-width="68px">
            <div class="mb8">
                <el-row>
                    <el-col>
                        <el-form-item>
                            <el-button icon="el-icon-view" size="mini" @click="clickStartRealPlay(undefined, hasLogin)"
                                type="primary"></el-button>
                            <!-- <el-button
                        icon="el-icon-view"
                        size="mini"
                        @click="changemode"
                        type="warning"
                        ></el-button
                      > -->
                            <el-button icon="el-icon-view" size="mini" @click="clickFullScreen"
                                type="info"></el-button>
                        </el-form-item>
                    </el-col>
                </el-row>
            </div>
        </el-form>
    </div>
</template>

<script>
export default {
    name: "hkVideo",
    inheritAttrs: false,
    props: {
        data: {
            type: Object || Array,
            require: true,
        },
        mode_info: {
            type: String,
            require: true,
        },
        newId: {
            type: String,
            require: true,
            default: "div123",
        },
        loading_time: {
            type: Number,
            require: true,
            default: 2000,
        },
    },
    computed: {
        activeIndex() {
            return this.$route.path;
        },
    },
    watch: {
        newId: {
            deep: true,
            immediate: true,
            handler(newVal, oldVal) {
                if (newVal) {
                    this.myNewId = newVal;
                }
            },
        },
        loading_time: {
            deep: true,
            immediate: true,
            handler(newVal, oldVal) {
                if (newVal) {
                    this.loadingtime = newVal;
                }
            },
        },
        data: {
            deep: true,
            immediate: true,
            handler(newVal, oldVal) {
                if (newVal) {
                    console.log("assign value", newVal);
                    this.ip = newVal.nvrAddress; //IP
                    if (this.activeIndex == "/videomonit/bottle/1") {
                        this.ip = "200.150.100.252";
                        // console.log("11111", newVal);
                    }
                    this.userName = newVal.userName; //nvr
                    this.passWord = newVal.passWord; //


                    switch (true) {
                        case this.ip.indexOf("200.150.100.252") >= 0: {
                            this.passWord = "ZG20241019";
                            break;
                        }
                    }
                    console.log(this.ip);

                    console.log('aaaaa', this.passWord);

                    if (newVal.rtspPort) this.rtspPort = newVal.rtspPort;
                    else this.rtspPort = 554; //
                    if (newVal.transStream) this.transStream = true;
                    else this.transStream = false;
                    // this.analogMode =newVal.nvrAddress.indexOf('192.168.20.248')<0? true:false;
                    //else this.analogMode = false;
                    // analogMode
                    if (newVal.analogMode) this.analogMode = true;
                    else this.analogMode = false;


                    console.log("analogMode", this.analogMode);
                    if (newVal.port) this.port = newVal.port; //
                    else this.port = 80; //80
                    this.iChannelID = !newVal.channelID ? 1 : newVal.channelID; //ID

                    //this.loadingTime = newVal.loadingTime;
                    if (newVal.title) this.title = newVal.title; //
                    else this.title = "";

                    if (newVal.streamType) this.streamType = newVal.streamType;
                    else this.streamType = 1;
                    if (newVal.videoWidth) this.videoWidth = newVal.videoWidth; //
                    if (newVal.videoHeight) this.videoHeight = newVal.videoHeight;
                    if (this.ip && this.port) {
                        // console.log("hkvideo", this.loadingTime);
                        if (!this.plugInInitialized) {
                            setTimeout(() => {
                                this.initS(); //
                                console.log("", this.loading_time);
                            }, this.loading_time);
                        } else {
                            //
                            this.clickLogin();
                        }
                    }
                }
            },
        },
    },
    data() {
        return {
            mode: this.mode_info,
            imgdialog: false,
            myNewId: "divccf",
            visible: true, //
            showSearch: true, //
            advanceMode: true, //
            queryParams: {}, //
            ip: "", //IP
            transStream: true, //
            rtspPort: 554, //rtsp
            userName: "", //admin
            passWord: "", //
            port: 80, //80
            iChannelID: 1, //id
            windowCount: 1, //1X1
            loadingTime: 2000, //
            timeOut: 2000, //
            videoWidth: 600, //
            videoHeight: 400, //
            fullSize: false, //
            title: "", //
            rtsp: "", //"rtsp://admin:ZG20241019@200.150.100.251:554/Streaming/Channels/101",
            startTime: "", //
            endTime: "", //
            g_iWndIndex: 0, //k
            timeRange: [], //
            streamType: 1, //1, 
            bZeroChannel: false, //
            analogMode: false, //
            streamTypeList: [
                { id: 1, label: "" },
                { id: 2, label: "" },
                { id: 3, label: "" },
                { id: 4, label: "" },
            ],
            startTime: "",
            endTime: "",
            plugInInitialized: false, //
            loading: true,
            hasLogin: false, //
        };
    },
    mounted() {
        console.log("webSDKPlugin/HCWebSDKPlugin.exe");
        // this.initS();
    },
    methods: {
        initS() {
            let that = this;
            if (!that.newId) {
                console.warn("hk video id empty, can not create hk video plugin.");
                return;
            }
            if (!that.ip || !that.port) {
                console.warn("hk video ip/port empty, can not init plugin.");
                return;
            }
            if (!this.plugInInitialized) {
                console.log("plugin initialized.");
                // 
                WebVideoCtrl.I_InitPlugin({
                    bWndFull: true, // true: false:
                    iWndowType: 1, //  1 1*1   22*2
                    cbSelWnd: function (xmlDoc) {
                        //
                        this.g_iWndIndex = parseInt(
                            $(xmlDoc).find("SelectWnd").eq(0).text(),
                            10
                        );
                        var szInfo = "" + this.g_iWndIndex;
                        console.log(szInfo);
                    },
                    cbDoubleClickWnd: function (iWndIndex, bFullScreen) {
                        this.iWndIndex = iWndIndex;
                        var szInfo = "" + iWndIndex;
                        if (!bFullScreen) {
                            szInfo = "" + iWndIndex;
                        }
                        console.log(szInfo);
                    },
                    cbEvent: function (iEventType, iParam1, iParam2) {
                        console.log(
                            ", eventType=" +
                            iEventType +
                            ",param1=" +
                            iParam1 +
                            ",p2=" +
                            iParam2
                        );
                        if (2 == iEventType) {
                            // 
                            console.log("" + iParam1 + "");
                        } else if (-1 == iEventType) {
                            console.log("" + iParam1 + "");
                        } else if (3001 == iEventType) {
                            this.clickStopRealPlay();
                            //this.clickStopRecord(g_szRecordType, iParam1);
                        }
                    },
                    cbInitPluginComplete: function () {
                        that.plugInInitialized = true;
                        WebVideoCtrl.I_InsertOBJECTPlugin(that.newId).then(
                            () => {
                                console.log("init ok.");
                                // setTimeout(() => {
                                //
                                that.clickLogin();
                                that.setVideoWindowResize(this.newId, "700", "500");
                                // }, 7000);
                                // 
                                WebVideoCtrl.I_CheckPluginVersion().then((bFlag) => {
                                    if (bFlag) {
                                    }
                                    //   this.loading = false;
                                });
                            },
                            () => {
                                let str1 =
                                    "HCWebSDKPlugin.exe";
                                that.$message.error(str1);
                                console.log(str1);
                                this.loading = false;
                            }
                        );
                    },
                });
            }
            //   setTimeout(() => {
            //     //   let cw = Math.round(document.body.clientWidth / 1920);
            //     //   let ch = Math.round(document.body.clientHeight / 1080);
            //     //   let width = parseInt(this.videoWidth * cw, 10);
            //     //   let height = parseInt(this.videoHeight * ch, 10);
            //     //   if (height <= 200) {
            //     //     height = 200;
            //     //   }
            //     //   if (height <= 200) {
            //     //     height = 200;
            //     //   }
            //     //   //let w1 = (window.innerWidth - document.getElementById(this.newId).offsetLeft) - 1700;
            //     //   //let w2 = document.getElementById(this.newId).clientHeight;
            //     //   console.log("00000===>", width, height);
            //     //   // 
            //     //   WebVideoCtrl.I_Resize(width, height);
            //     //   this.setVideoWindowResize(this.newId, width, height);
            //     this.clickLogin();
            //   }, 700);
        },
        clickFullScreen() {
            WebVideoCtrl.I_FullScreen(true).then(
                () => {
                    console.log("");
                },
                (oError) => {
                    console.log("", oError.errorCode, oError.errorMsg);
                }
            );
        },
        // windowchange
        setVideoWindowResize(pid, width, height) {
            if (!pid) return;
            var elment = document.getElementById(pid);
            if (elment) {
                elment.style.width = width + "px";
                elment.style.height = height + "px";
            }
        },
        // 
        clickLogin() {
            let that = this;
            that.hasLogin = false;
            // this.hasLogin = false;
            this.loading = true;
            var szDeviceIdentify = this.ip + "_" + this.port;
            WebVideoCtrl.I_Login(
                this.ip,
                1,
                this.port,
                this.userName,
                this.passWord,
                {
                    timeout: this.timeOut,
                    success: function (xmlDoc) {
                        that.hasLogin = true;
                        console.log(szDeviceIdentify + " ");
                        // if (that.mode == "preview") {
                        console.log(" this.hasLogin", that.hasLogin);
                        console.log("do start realplay...");
                        // setTimeout(function () {
                        // 
                        // setTimeout(function () {
                        //   setTimeout(function () {
                        // 
                        // that.getChannelInfo();
                        //   }, 1000);
                        that.getDevicePort(that.hasLogin);
                        // }, 10);
                    },
                    error: function (err) {
                        console.log("-err===>", err);
                        if (err.errorCode == 2001) {
                            that.hasLogin = true;
                            console.log(szDeviceIdentify + " ");
                            console.log("do start realplay...");
                            // 
                            that.getDevicePort(that.hasLogin);
                        } else {
                            that.$message.error(
                                "/"
                            );
                            // that.hasLogin = false;
                        }
                        that.loading = false;
                    },
                }
            );
        },

        // 
        getDevicePort(hasLogin) {
            if (null == this.ip) {
                return;
            }
            WebVideoCtrl.I_GetDevicePort(this.ip).then((oPort) => {
                console.log("152", oPort);
                // channel
                this.getChannelInfo(hasLogin);
            });
        },
        // 
        getChannelInfo(hasLogin) {
            let that = this;
            var szDeviceIdentify = this.ip + "_" + this.port;
            if (null == szDeviceIdentify) {
                return;
            }
            console.log("", szDeviceIdentify);

            // 
            WebVideoCtrl.I_GetAnalogChannelInfo(szDeviceIdentify, {
                success: function (xmlDoc) {
                    setTimeout(() => {
                        that.clickStartRealPlay(undefined, hasLogin);
                        console.log("");

                        // console.log("szDeviceIdentify==============>", hasLogin);
                        //that.clickStartRealPlay(undefined, hasLogin);
                    }, that.loadingTime);
                },
                error: function (oError) {
                    // setTimeout(() => {
                    // console.log("szDeviceIdentify==============>", hasLogin);
                    //that.clickStartRealPlay(undefined, hasLogin);
                    // }, 3000);
                    console.log("");

                    console.log(
                        szDeviceIdentify + "",
                        oError.errorCode,
                        oError.errorMsg
                    );
                },
            });

            // 
            WebVideoCtrl.I_GetDigitalChannelInfo(szDeviceIdentify, {
                success: function (xmlDoc) {
                    var oChannels = $(xmlDoc).find("InputProxyChannelStatus");
                    console.log("");

                    // const that = this;
                    // console.log("xmlDoc", xmlDoc);
                    // $.each(oChannels, function (i) {
                    //   const id = $(this).find("id").eq(0).text();

                    //   let name = $(this).find("name").eq(0).text();
                    //   const online = $(this).find("online").eq(0).text();
                    //   if (online === "false") {
                    //     // 
                    //     return true;
                    //   }
                    //   if (name === "") {
                    //     name = "IPCamera " + (i < 9 ? "0" + (i + 1) : i + 1);
                    //   }
                    //   console.log(id, name);
                    //   console.log(online);

                    //   // that.channelsList.push({ id, name, bZero: false });
                    //   //   console.log(that.channelsList + " ");
                    // });

                    setTimeout(() => {
                        if (!that.analogMode) that.clickStartRealPlay(undefined, hasLogin);
                        // console.log("szDeviceIdentify==============>", hasLogin);
                        //that.clickStartRealPlay(undefined, hasLogin);
                    }, that.loadingTime);
                },
                error: function (oError) {
                    console.log(
                        szDeviceIdentify + " ",
                        oError.errorCode,
                        oError.errorMsg
                    );
                },
            });
        },
        // ()
        clickStartRealPlay(iStreamType, hasLogin) {
            console.log("", hasLogin);
            if (this.plugInInitialized && hasLogin) {
                let that = this;
                that.loading = true;
                var oWndInfo = WebVideoCtrl.I_GetWindowStatus(this.g_iWndIndex) || null;
                if (!this.ip || !this.port) return;

                var szDeviceIdentify = this.ip + "_" + this.port;

                if ("undefined" === iStreamType) {
                    this.streamType = 1;
                } else {
                    this.streamType = iStreamType;
                }
                if (null == szDeviceIdentify) {
                    return;
                }
                var option = {
                    iStreamType: that.streamType,
                    iChannelID: that.iChannelID,
                    bZeroChannel: that.bZeroChannel,
                    // iRtspPort: 10311, // RTSP
                    success: function () {
                        //WebVideoCtrl.I_Logout(szDeviceIdentify);
                        console.log("");
                        that.loading = false;
                    },
                    error: function (oError) {
                        that.$message.error(
                            szDeviceIdentify + ""
                        );
                        console.log(szDeviceIdentify + "");
                        console.log("oError", oError);
                        that.loading = false;
                    },
                };
                console.log("start real play with option:", option);
                option.iStreamType = 1;
                var startRealPlay = function () {
                    WebVideoCtrl.I_StartRealPlay(szDeviceIdentify, option);
                };

                if (oWndInfo != null) {
                    // 
                    WebVideoCtrl.I_Stop({
                        success: function () {
                            startRealPlay();
                        },
                    });
                } else {
                    startRealPlay();
                }
            } else if (!hasLogin) {
                this.$message.error("");
            }
            if (!this.plugInInitialized) {
                this.$message.error(
                    "HCWebSDKPlugin.exe"
                );
            }
        },
        // 
        clickStopRealPlay() {
            WebVideoCtrl.I_Stop({
                success: function () {
                    console.log("");
                },
                error: function (oError) {
                    console.log(" ");
                },
            });
        },
        closeVideo() {
            console.log("plugInInitialized", this.plugInInitialized);
            if (this.plugInInitialized) {
                try {
                    WebVideoCtrl.I_StopAllPlay(); //
                    var szDeviceIdentify = this.ip + "_" + this.port;
                    WebVideoCtrl.I_Logout(szDeviceIdentify); //
                    WebVideoCtrl.I_DestroyPlugin(szDeviceIdentify); // 
                } catch (e) {
                    console.log(e.$message);
                }
                this.plugInInitialized = false;
                window.location.reload(); //
            }
        },

        loginOut() {
            WebVideoCtrl.I_Logout(this.ip + "_" + this.port).then(
                () => {
                    console.log("");
                    WebVideoCtrl.I_DestroyPlugin();
                },
                () => {
                    console.log("");
                }
            );
        },
        stopAllPlay() {
            WebVideoCtrl.I_StopAllPlay();
        },
        breakDom() {
            // console.log('aaaaa',this.plugInInitialized)
            // if (this.plugInInitialized) {
            //   WebVideoCtrl.I;
            WebVideoCtrl.I_DestroyPlugin();
            // this.plugInInitialized = false;
            // }
        },
        viewReload() {
            window.location.reload();
        },
    },
    beforeDestroy() {
        this.loginOut();
        this.stopAllPlay();
        window.removeEventListener(this.viewReload);
    },
    destroyed() {
        // setTimeout(() => {
        //   this.breakDom();
        // }, 1000);
    },
};
</script>

<style></style>