<template>
  <div>
    <div class="machineTable">
      <img
        src="../../../assets/images/loading.gif"
        class="loadgif"
        id="show_loading2"
      />
      <p></p>
    </div>
    <el-row justify="space-between">
      <el-col style="width: 2rem" v-if="!showSelect"> </el-col>
      <el-col :span="18">
        <el-popconfirm
          confirm-button-text=""
          cancel-button-text=""
          icon="el-icon-info"
          title=""
          cancel-button-type="primary"
          @confirm="showSelectFunc('')"
          @cancel="showSelectFunc('')"
          :disabled="loading"
        >
          <el-button
            type="primary"
            class="plusicon"
            v-if="!showSelect"
            slot="reference"
            :disabled="loading"
          >
            <span></span>
          </el-button>
        </el-popconfirm>
        <span style="color: white"
          >1
          2</span
        >

        <div v-if="showSelect" style="width: 5rem">
          <el-button
            type="text"
            @click="confirmSelectFunc"
            class="plusicon"
            v-if="showSelect"
            style="margin-right: 0.1rem"
          >
            <span></span>
          </el-button>
          <el-button
            type="text"
            @click="showSelectFunc('')"
            class="plusicon"
            v-if="showSelect"
            style="margin-right: 0.1rem; color: grey"
          >
            <span></span>
          </el-button>
          <span class="report_msg"
            >
            <span style="font-weight: bold"> {{ messageinfo }} </span
            ></span
          >
        </div>
      </el-col>
      <el-button
        v-if="!showsearch && !showSelect"
        type="primary"
        @click="
          showsearch = true;
          heightValue = 'calc(100vh - 3.8rem)';
        "
        icon="el-icon-search"
        class="plusicon"
        title=""
        style="float: right"
      ></el-button>
      <el-col :span="24" v-else-if="!showSelect">
        <filtercom
          :filterinfo="[
            '',
            '',
            '',
            '',
            '',
            '',
            '',
          ]"
          ref="filtercom"
          @searchFunc="searchFunc"
        ></filtercom>
      </el-col>
    </el-row>

    <div class="table-container">
      <el-table
        bordered
        row-class-name="rowstyle"
        :data="paginatedData"
        style="width: 100%; background: transparent"
        :cell-class-name="getCellClassName"
        :height="!showsearch ? 'calc(100vh - 3.3rem)' : 'calc(100vh - 3.3rem)'"
        v-loading="loading"
        :key="showSelect"
        @selection-change="handleSelectionChange"
        ref="multipleTable"
      >
        <el-table-column label="" v-if="!showSelect" minWidth="100rem">
          <template #default="scope">
            <!-- <el-button
                v-if="scope.row[''] != ''"
                @click="confirmDeleteRow(scope.row)"
                type="text"
                size="small"
                class="deletebtn"
              >
                
              </el-button> -->
            <el-button
              v-if="scope.row[''] == ''"
              @click="editRow(scope.row)"
              type="text"
              size="small"
              :disabled="scope.row['ID'] == null"
              :style="{
                'font-size': '0.175rem',
                'margin-right':
                  scope.row[''] != '' ? '0rem' : '0.125rem',
              }"
            >
              
            </el-button>
            <el-button
              @click="modifyStatus(scope.row)"
              type="text"
              size="small"
              :style="{
                'font-size': '0.175rem',
                'margin-right':
                  scope.row[''] != '' ? '0rem' : '0.125rem',
              }"
            >
              <span v-if="scope.row[''] == ''" style="color: yellow"
                ></span
              >
              <span
                v-if="scope.row[''] == ''"
                style="color: #1dc51d"
                ></span
              >
            </el-button>
            <el-button
              @click="viewhistory(scope.row)"
              type="text"
              size="small"
              style="
                font-size: 0.175rem;
                color: rgb(212 158 78);
                margin-left: 0;
              "
            >
              
            </el-button>
          </template>
        </el-table-column>
        <el-table-column type="selection" width="55" v-if="showSelect">
        </el-table-column>
        <el-table-column label="">
          <template #default="scope">
            {{ scope.row["addFlag"] == 1 ? "" : "" }}
          </template>
        </el-table-column>
        <el-table-column
          fit
          v-for="(column, index) in columns"
          :key="index"
          :prop="column.prop"
          :label="column.label"
          :sortable="column.sortable"
          v-if="column.label != 'ID' && column.label != 'ID'"
        >
          <template #default="scope">
            <div v-if="!scope.row.editing">
              <div
                v-if="
                  column.prop == '' || column.prop == ''
                "
                @click="showbigimg(scope.row[column.prop])"
              >
                <img
                  :src="scope.row[column.prop]"
                  style="width: 0.5rem"
                  v-if="scope.row[column.prop]"
                />
                <span v-else></span>
              </div>
              <div v-if="column.prop == ''">
                <img
                  :src="
                    scope.row[column.prop] != 'grey'
                      ? require(`../../../assets/images/${
                          scope.row[column.prop]
                        }light.png`)
                      : require(`../../../assets/images/greenlight.png`)
                  "
                  class="lightimage"
                  :style="
                    scope.row[column.prop] != 'grey'
                      ? ''
                      : 'filter: grayscale(100%)'
                  "
                />
              </div>
              <span
                v-else-if="
                  column.prop != '' &&
                  column.prop != '' &&
                  column.prop != ''
                "
              >
                <span v-if="column.prop.indexOf('') >= 0">{{
                  String(scope.row[column.prop]).replace("T00:00:00", "")
                }}</span>
                <span v-else>
                  {{ scope.row[column.prop] }}
                </span>
              </span>
            </div>

            <!-- <div v-else>
                  <el-input
                    v-model="scope.row[column.prop]"
                    
                  ></el-input>
                </div> -->
          </template>
        </el-table-column>
      </el-table>
      <el-button
        type="primary"
        @click="
          $downloadExcel({
            tableData: tabledata,
            title: '',
          })
        "
        class="plusicon"
        size="small"
        ></el-button
      >
    </div>

    <el-dialog
      :title="operatetitle"
      :visible="addRowDialogVisible"
      :modal="false"
      top="0.8rem"
      :close-on-click-modal="false"
      @close="closeDialog"
    >
      <div class="dialog-content">
        <el-form
          :model="newRow"
          label-position="top"
          ref="newRow"
          :rules="rules_data"
        >
          <el-form-item
            v-for="column in columns"
            :key="column.prop"
            :label="column.label"
            :prop="column.prop"
            v-if="column.label != 'ID' && column.prop != 'ID'"
          >
            <div v-if="column.prop == ''">
              <el-radio-group v-model="newRow[column.prop]" disabled>
                <el-radio
                  v-for="city in checkdata1"
                  :label="city"
                  :key="city"
                  >{{ city }}</el-radio
                >
              </el-radio-group>
            </div>
            <el-select
              v-if="column.prop == ''"
              v-model="newRow[column.prop]"
              style="width: 8rem"
            >
              <el-option
                v-for="item in deviceKind"
                :label="item"
                :value="item"
                :key="item"
              ></el-option>
            </el-select>
            <el-date-picker
              v-if="column.prop.indexOf('') >= 0"
              v-model="newRow[column.prop]"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
            >
            </el-date-picker>
            <div
              v-if="
                column.prop == '' || column.prop == ''
              "
            >
              <Uploadocom
                v-if="!newRow[column.prop]"
                :uploadAction="'/api/function/upload'"
                :showloading2="showloading2"
                @upload="listenUpload"
                @imageshow="(data) => imageshowFunc(data, column.prop)"
                ref="uploadocom"
              ></Uploadocom>
              <div style="width: 1rem" v-else>
                <img
                  :src="newRow[column.prop]"
                  style="width: 1rem; height: 1rem"
                />
                <div style="width: 1rem">
                  <span
                    style="color: #247cc3; cursor: pointer"
                    @click="cancelImg(column.prop)"
                    ></span
                  >
                </div>
              </div>
            </div>
            <el-input
              v-else-if="
                column.prop != '' &&
                column.prop != '' &&
                column.prop != '' &&
                column.prop.indexOf('') < 0
              "
              v-model="newRow[column.prop]"
              :placeholder="'' + column.label"
              style="width: 8rem"
              @change="changeBasic(column.prop)"
              :disabled="check_datas.indexOf(column.prop) < 0"
            ></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button
          @click="addRow('newRow')"
          type="primary"
          :loading="loadingbtn"
          ></el-button
        >
        <el-button @click="closeDialog"></el-button>
      </span>
    </el-dialog>

    <el-dialog
      :modal="false"
      title=""
      :visible.sync="confirmDeleteDialogVisible"
      @close="cancelDelete"
      width="5rem"
    >
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelDelete"></el-button>
        <el-button type="danger" @click="deleteRow(confirmedRow)"
          ></el-button
        >
      </span>
    </el-dialog>

    <el-dialog
      :modal="false"
      title=""
      :visible.sync="imgdialog"
      @close="imgdialog = false"
      custom-class="imgdialog"
      top="1rem"
    >
      <div style="width: 100%">
        <img :src="currimgsrc" style="width: 100%" />
      </div>
    </el-dialog>

    <el-pagination
      v-if="tableData.length > pageSize"
      class="el-pagin"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="total"
      layout="total, prev, pager, next, sizes"
      :page-sizes="pageSizes"
      ref="pangination_n"
    />
    <el-dialog
      :modal="false"
      :visible.sync="his_visible"
      title=""
      fullscreen
      custom-class="historydialog"
    >
      <historyDialog
        ref="history"
        :current_info="newRow"
        v-if="his_visible"
      ></historyDialog>
    </el-dialog>
  </div>
</template>

<script>
import Filtercom from "@/views/components/filtercom.vue";
import Uploadocom from "../../components/upload.vue";
import historyDialog from "./scrabDialog.vue";

export default {
  props: ["column", "tabledata", "operate", "historyDialog"],

  components: { Uploadocom, Filtercom, historyDialog },
  data() {
    return {
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      pageSizes: [],
      total: 0,
      columns: [],
      addRowDialogVisible: false,
      newRow: {},
      confirmDeleteDialogVisible: false,
      confirmedRow: null,
      value: "",
      loadingbtn: false,
      deviceKind: [
        {
          label: "",
          value: 1,
        },
        {
          label: "",
          value: 2,
        },
      ],
      deviceKind2: ["", ""],
      showloading2: "show_loading2",
      imgdialog: false,
      currimgsrc: false,
      checkdata1: ["", "", ""],
      checkedData1: "",
      operatetitle: "",
      rules_data: {},
      showsearch: false,
      change_base: false,
      loading: false,
      loading_status: false,
      check_datas: [
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      his_visible: false,
      showSelect: false,
      showSelectvalue: [],
      pre_currentPage: 1,
      messageinfo: "",
    };
  },
  methods: {
      showSelectFunc(msg) {
        this.currentPage=1
        this.messageinfo = msg;
        this.$emit("chooseReport", msg);
        this.showSelect = !this.showSelect;
         if(msg==''){
          this.showSelectvalue=[]
         }
        this.$nextTick(() => {
          this.$forceUpdate(); //  Vue 
        });
      },
    confirmSelectFunc(msg) {
      this.$functionConfrim(this.showSelectvalue).then((result) => {
        console.log(result);
        if (result.status == "yes") {
          this.$emit("reportInfo", {
            checkevalue: this.showSelectvalue,
            operate: this.messageinfo,
          });
          //   this.$refs.multipleTable.clearSelection();
        }
      });
    },
    handleSelectionChange(val) {
      console.log(val);
      this.showSelectvalue = val;
    },
    viewhistory(row) {
      this.his_visible = true;
      this.newRow = Object.assign({}, row);
    },
    // 
    modifyStatus(row) {
      //   this.loading_status = this.$loading({
      //     lock: false,
      //     text: "",
      //     spinner: "el-icon-loading",
      //     background: "rgba(0, 0, 0, 0.2)",
      //     target: document.querySelector(".table-container"),
      //     customClass: "status_loading",
      //   });
      this.newRow = JSON.parse(JSON.stringify(row));

      if (row[""] == "") {
        // this.$emit("addinfo", {
        //   rowinfo: this.newRow,
        //   operate: "",
        // });
        this.addRowDialogVisible = true;
        //   this.newRow = JSON.parse(JSON.stringify(row));
        this.$nextTick(() => {
          this.newRow = Object.assign({}, row);
          let check = ["", "", ""];
          for (let i = 0; i < check.length; i++) {
            this.newRow[check[i]] = "";
          }
          this.newRow[""] = "";
          this.rules_data = this.$formatFormRules([], this.newRow, {});

          this.operatetitle = "";
        });
      } else if (row[""] == "") {
        this.$emit("deleterow", {
          rowinfo: this.newRow,
          operate: "",
        });
      }
    },
    changeBasic(prop) {
      //   console.log(prop);
      if (
        ["", "", "", "", ""].indexOf(
          prop
        ) >= 0
      ) {
        this.change_base = true;
      }
    },
    searchFunc() {
      let params = Object.assign({}, this.$refs.filtercom.filterparams);
      this.$emit("searchparams", params);
    },
    openAddRowDialog() {
      this.addRowDialogVisible = true;
      this.operatetitle = "";
      this.$nextTick(() => {
        this.formatRow();
        this.newRow[""] = "";

        this.rules_data = this.$formatFormRules([], this.newRow, {});
      });
    },
    //  pageSizes 
    setPageSizes() {
      const totalItems = this.tableData.length;
      this.pageSizes = [];

      //  [10, 20, 30, ...] 
      for (let i = 10; i <= totalItems; i += 10) {
        this.pageSizes.push(i);
      }

      // 10
      if (this.pageSizes.length === 0) {
        this.pageSizes.push(10);
      }
    },
    closeDialog() {
      this.addRowDialogVisible = false;
      //   this.operatetitle = "";
    },
    cancelImg(column) {
      this.newRow[column] = "";
    },
    imageshowFunc(msg, column) {
      this.newRow[column] = msg;
    },
    showbigimg(src) {
      this.imgdialog = true;
      this.currimgsrc = src;
    },
    // update
    listenUpload() {
      // this.getSAPTable();
    },

    handleCurrentChange(page) {
      // console.log(this.showSelectvalue);
      if (this.showSelectvalue.length) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning",
        }).then(() => {
          // this.pageSize=0
          // console.log(this.$refs.pangination_n.internalCurrentPage);
          this.$nextTick(() => {
            // this.pageSize=10
            this.currentPage = this.pre_currentPage;
            this.$refs.pangination_n.internalCurrentPage = this.currentPage;
            this.$forceUpdate();
          });
          // console.log(this.currentPage,this.pre_currentPage);
        });
      } else {
        this.currentPage = page;
        this.pre_currentPage = page;
      }
    },
    handleSizeChange(size) {
      this.pageSize = size;
    },
    viewDetails(row) {
      console.log(":", row);
    },
    // 
    editRow(row) {
      this.addRowDialogVisible = true;
      //   this.newRow = JSON.parse(JSON.stringify(row));
      this.$nextTick(() => {
        this.newRow = Object.assign({}, row);
        this.rules_data = this.$formatFormRules([], this.newRow, {});
        // this.rules_data = {
        //   : [
        //     {
        //       required: true,
        //       message: "",
        //       trigger: "change",
        //     },
        //   ],
        // };
      });
      this.operatetitle = "";
      //   console.log(this.newRow);
    },
    saveRow(row) {
      row.editing = false;
      console.log(":", row);
    },
    confirmDeleteRow(row) {
      if (this.tableData.length == 1) {
        this.$message({
          type: "warning",
          message: "",
          //   duration: 0,
          showClose: true,
        });
      } else {
        this.confirmDeleteDialogVisible = true;
        this.confirmedRow = row; // 
      }
    },
    cancelDelete() {
      this.confirmDeleteDialogVisible = false;
      this.confirmedRow = null; // 
    },
    deleteRow(row) {
      console.log(row);
      this.$emit("deleterow", { rowinfo: row, operate: "" });
    },
    addRow(formname) {
      console.log(this.newRow);
      // console.log(this.$refs[formname])
      this.$refs[formname].validate((valid) => {
        // console.log(valid)
        if (valid) {
          this.$emit("addinfo", {
            rowinfo: this.newRow,
            operate: this.operatetitle,
          });
          this.change_base = false;
        } else {
          this.$message({
            message: "",
            type: "error",
          });
          return false;
        }
      });
    },
    getCellClassName({ row, column, rowIndex, columnIndex }) {
      if (row.age === "" && columnIndex === 2) {
        return "orange-warning"; // 
      } else {
        return "";
      }
    },
    // 
    formatRow() {
      this.newRow = this.columns.reduce((obj, column) => {
        obj[column.prop] = "";
        return obj;
      }, {});
    },
    validateNumber(rule, value, callback) {
      const number = parseInt(value, 10);
      if (number) {
        callback();
      }
    },
  },
  computed: {
    paginatedData() {
      this.columns = this.column;
      this.tableData = this.tabledata.map((item) => ({
        ...item,
      }));
      this.total = this.tableData.length;
      this.setPageSizes();

      const start = (this.currentPage - 1) * this.pageSize;
      return this.tableData.slice(start, start + this.pageSize);
    },
  },
  mounted() {},
};
</script>

<style lang="less"></style>
